import React, {Component} from 'react';
import 'moment/locale/tr';
import {Col, Input, Row} from 'reactstrap';
import {authorizationWays, showAxiosError} from "../../../../helper/helper";
import {requestK} from "../../../../helper/APIUtils";
import DefaultModal from "../../../../components/DefaultModal";
import Alert from "react-s-alert";
import {Decimal} from 'decimal.js';
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import axios from 'axios';
import Dropzone from 'react-dropzone';
import Datetime from "react-datetime";
import moment from "moment";
import Joyride from "react-joyride";
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import Table from "../../../../components/Table/Table";
import {addCustomFields, genderData, renewListForDepartment, statusTypeFilterData} from "../../common/PeykHelper";
import DropdownCheck from "../../../../components/Table/DropdownCheck";

class UploadBordroFileModalWithoutTckn extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selection: [],
            selectedAll: false,
            loading: false,
            manual: false,
            content: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 5,
            titleFilterList: [],
            fileList: [],
            index: 0,
            process: false,
            realIndex: 0,
            year: null,
            month: null,
            disableZone: false,
            steps: [
                {
                    target: '.modal-content',
                    content: "alanlarnza cret bordrosu gnderimi yapmak iin, toplu veya ayr ayr olan cret bordrolarn pdf veya xlsx uzantl olarak ykleme yapnz.",
                    placement: "center",
                },
                {
                    target: '.form-control',
                    content: "Bordro dnemini buradan seebilirsiniz.",
                    placement: "auto",
                },
                {
                    target: '.dropzoneInnerArea',
                    content: 'cret bordrolarnz srkle-brak veya bu alana tklayarak ykleyebilirsiniz.',
                    placement: "auto",
                },
                {
                    target: '.kaydet',
                    content: 'Dnem ve yklenecek dosyalar seildikten sonra yklemeyi balatabilirsiniz.',
                    placement: "auto",
                }
            ],
            steps2: [
                {
                    target: '.modal-content',
                    content: "Seilen bordro dnemine dosya eklemesi yapmak iin, toplu veya ayr ayr olan cret bordrolarn pdf veya xlsx uzantl olarak ykleme yapnz.",
                    placement: "center",
                },
                {
                    target: '.dropzoneInnerArea',
                    content: 'cret bordrolarnz srkle-brak veya bu alana tklayarak ykleyebilirsiniz.',
                    placement: "auto",
                },
                {
                    target: '.kaydet',
                    content: 'Yklenecek dosyalar seildikten sonra yklemeyi balatabilirsiniz.',
                    placement: "auto",
                }
            ],
            locale: {
                back: 'Geri',
                close: 'Kapat',
                last: 'Son',
                next: 'leri',
                skip: 'Atla',
            },
            joyrideActive: false,
            shouldRun: true,
        };
        this.onDrop = this.onDrop.bind(this);
        this.callback = this.callback.bind(this);
        this.onCancel = this.onCancel.bind(this);
        this.changeLocation = this.changeLocation.bind(this);
        this.sendNextBordroFile = this.sendNextBordroFile.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.searchWithKey = this.searchWithKey.bind(this);
        this.fetchData = this.fetchData.bind(this);
        this.setSelectedDepts = this.setSelectedDepts.bind(this);
        this.toggleCheckboxChange = this.toggleCheckboxChange.bind(this);
    }

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
    }

    isSelected(id) {
        return (
            (this.props.selectedList && this.props.selectedList.indexOf(id) !== -1) ||
            (this.state.selection && this.state.selection.indexOf(id) !== -1)
        );
    }

    createCustomFields = (searchQuery) => {
        let customFieldsQuery = "";
        if (searchQuery.length) {
            let searchList = searchQuery.toString().split("&");
            searchQuery = "";
            searchList.map(e => {
                if (e.length) {
                    if (e.includes("cstmfld")) {
                        customFieldsQuery = customFieldsQuery + (customFieldsQuery.length ? "#" : "") + e.replace("cstmfld", "");
                    } else {
                        searchQuery = searchQuery + "&" + e;
                    }
                }
            });
        }
        return [searchQuery, customFieldsQuery];
    };

    getTrStyles(row) {
        return {
            /*    backgroundColor: (
                    (this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1) ? "#ffc" : "#f9f9f9"
                )*/
        };
    }

    setSelectedDepts(url) {
        let self = this;
        let selectedDepts = self.state.selectedDepts;
        if (selectedDepts) {
            Object.keys(selectedDepts).map(dept => {
                if (selectedDepts[dept]) {
                    if (self.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE) {
                        url = url + '&expenditureDepartment_id=' + dept;
                    } else {
                        url = url + '&department_id=' + dept;
                    }
                }
            });
        }
        return url;
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let searchQueryResult = self.createCustomFields(searchQuery);
        let url = '?page=' + page + '&size=' + size + searchQueryResult[0] + sortStr +
            '&customFields=' + searchQueryResult[1];
        url = self.setSelectedDepts(url);
        let params = {
            url: '/employee/readActiveEmployeeAll' + url,
            method: "get",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    content: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false,
                    searching: false
                });
            })
            .catch(function (error) {
                self.setState({loading: false, searching: false});
                showAxiosError(error);
            });
    }

    searchWithKey(e) {
        this.setState({searchKey: e.target.value, manual: true});
    }

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true,
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.removeMonth === true && !!this.state.sessionMonth) {
            this.setState({sessionMonth: null});
            this.props.setRemoveMonthOff();
        }
    }

    zipAndUploadFiles = () => {
        let self = this;
        let fileList = self.state.fileList;
        const zip = require('jszip')();
        fileList.forEach(file => {
            zip.file(file.name, file.content);
        });
        zip.generateAsync({
            type: 'arrayBuffer',
            compression: 'DEFLATE',
            compressionOptions: {
                level: 9
            }
        }).then(content => {
            let file = {
                content:
                    new File(
                        [new Blob([content], {type: "application/zip"})],
                        "yuklenenDokumanlar.zip",
                        {type: "application/zip", lastModified: Date.now()}),
                name: "zipFile.zip",
                size: 1
            };
            fileList = [];
            fileList.push(file);
            let formData = new FormData();
            formData.append("content", file.content);
            formData.append("id", self.props.id ? self.props.id : self.state.id);
            formData.append("totalCount", 1);
            formData.append("employeeUserIds",self.state.selection);

            let params = {
                url: `/bordrosession/sendBordroFiles`,
                method: 'post',
                cancelToken: self._source.token,
                data: formData
            };
            fileList[0] = {
                status: "processing",
                statusText: "Gnderim balatld.",
                name: file.name,
                size: file.size,
                content: file.content,
            };
            self.setState({btnDisabled: true, fileList});
            requestK(params)
                .then(function (response) {
                    let document = fileList[0];
                    fileList[0] = {
                        status: "success",
                        statusText: "Gnderim baarl.",
                        name: document.name,
                        size: document.size,
                        content: document.content,
                    };
                    self.setState({fileList, id: response.data, btnDisabled: false});
                    self.props.toggleModal(true, true);
                    if (self.props.newSession) {
                        self.props.toggleModal(false, true);
                        self.props.history.push(`/${self.props.peykName}/bordroimport/${self.state.id}`);
                        self.props.loadBordroSession();
                    }
                })
                .catch(function (error) {
                    if (!axios.isCancel(error)) {
                        file.status = "error";
                        file.statusText = error.data;
                        fileList[0] = {
                            status: "error",
                            statusText: error.data,
                            name: file.name,
                            size: file.size,
                            content: file.content,
                        };
                        self.setState({fileList: fileList, btnDisabled: false});
                        showAxiosError(error);
                    }
                });

        });
    };


    sendNextBordroFile(index, firstCycle, total) {
        if (this.props.newSession && !moment(this.state.sessionMonth, "YYYY/MM", true).isValid()) {
            showAxiosError({message: 'cret Bordrosu Dnemi alanna YYYY/AA formatnda geerli bir deer giriniz.'});
        } else {
            let self = this;
            this._source = axios.CancelToken.source();
            let fileList = self.state.fileList;
            let totalCount;
            if (total === 0)
                totalCount = fileList.length;
            else
                totalCount = total;
            if (index < fileList.length) {
                let document1 = fileList[index];
                if(document1.size< 75 * 1024 * 1024){
                    let formData = new FormData();
                    formData.append("content", document1.content);
                    formData.append("id", self.props.id ? self.props.id : self.state.id);
                    formData.append("totalCount", totalCount);
                    formData.append("employeeUserIds", self.state.selection);

                    let params = {
                        url: `/bordrosession/sendBordroFiles`,
                        method: 'post',
                        cancelToken: self._source.token,
                        data: formData
                    };
                    fileList[index] = {
                        status: "processing",
                        statusText: "Gnderim balatld.",
                        name: document1.name,
                        size: document1.size,
                        content: document1.content,
                    };
                    self.setState({btnDisabled: true, fileList: fileList});
                    requestK(params)
                        .then(function (response) {
                            let document = fileList[index];
                            fileList[index] = {
                                status: "success",
                                statusText: "Gnderim baarl.",
                                name: document.name,
                                size: document.size,
                                content: document.content,
                            };
                            self.setState({fileList, index: (index + 1)});
                            firstCycle && self.setState({id: response.data});
                            if (index + 1 < fileList.length) {
                                if ((index + 1) % 5 === 0) {
                                    fileList.splice(0, 4);
                                    self.setState({realIndex: (self.state.realIndex + 4)});
                                    self.sendNextBordroFile((index + 1) - 4, false, totalCount);
                                } else {
                                    self.sendNextBordroFile(index + 1, false, totalCount);
                                }
                            } else {
                                self.setState({btnDisabled: false, index: index + 1});
                                if (self.props.newSession) {
                                    self.props.toggleModal(false, true);
                                    self.props.history.push(`/${self.props.peykName}/bordroimport/${self.state.id}`);
                                    self.props.loadBordroSession();
                                } else {
                                    if (fileList[index].status === "success")
                                        self.props.toggleModalWithTimeout(false, true);
                                }
                            }
                        })
                        .catch(function (error) {
                            if (!axios.isCancel(error)) {
                                document1.status = "error";
                                document1.statusText = error.data;
                                fileList[index] = {
                                    status: "error",
                                    statusText: error.data,
                                    name: document1.name,
                                    size: document1.size,
                                    content: document1.content,
                                };
                                self.setState({fileList: fileList, btnDisabled: false});
                                showAxiosError(error);
                            }
                        });
                }else{
                    Alert.error("Yklenen dosya boyutu 75MB' tan kk olmaldr", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000
                    });
                }
            } else {
                Alert.success('Tm dokmanlar kaydedildi.', {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }

        }
    }

    onCancel() {
        if (this._source)
            this._source.cancel();

        if (this.state.index > 0) {
            this.props.toggleModal(false, true);
        } else {
            this.props.toggleModal(false, false);
        }
    }

    onDrop(acceptedFiles, rejectedFiles) {
        let self = this;
        if (!!acceptedFiles) {
            self.setState({processing: true, uploadBtnDisabled: true, fileList: []});
            let fileList = [];
            for (let i = 0; i < acceptedFiles.length; i++) {
                fileList.push({content: acceptedFiles[i], name: acceptedFiles[i].name, size: acceptedFiles[i].size});
            }
            self.setState({processing: false, uploadBtnDisabled: false, fileList: fileList, index: 0, realIndex: 0});
        }
    }

    getStatusRender(status) {
        let className = (status === "processing" ? "spinner fa-spin" : (status === "success" ? "check" : (status === "error" ? "close" : "pause")));
        let color = (status === "processing" ? "dark" : (status === "success" ? "green" : (status === "error" ? "red" : "dark")));
        return (<i className={"fas fa-" + className} style={{color: color, paddingRight: "4px"}}></i>);
    }

    // activeJoyride(){
    //     this.setState({joyrideActive:true})
    // }

    activateJoyride() {
        if (this.state.joyrideActive === false)
            this.setState({joyrideActive: true})
        // else
        //     this.setState({ joyrideActive: false })
    }

    callback(data) {
        if (data.action === 'close' && data.type === 'step:after') {
            this.setState({shouldRun: false});
        }
        if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
            this.setState({shouldRun: true});
            this.setState({joyrideActive: false});
            this.activateJoyride();
        }
    }

    changeLocation(e) {
        this.setState({location: e.target.value});
    }

    render() {
        let steps = this.props && this.props.newSession ? this.state.steps : this.state.steps2;
        let locale = this.state.locale;
        let pageIdList = [], dublicateList = [], tempList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                if (!e.name || e.statusType === "ACTIVE") {
                    pageIdList.push(e.id);
                }
                if (!tempList.includes(e.tckn)) {
                    tempList.push(e.tckn);
                } else if (!dublicateList.includes(e.tckn)) {
                    dublicateList.push(e.tckn)
                }
            });
        }
        const fieldList = [
            {
                label: "sim",
                accessor: "name",
                type: "text",
                operator: "=",
            },
            {
                label: "TCKN",
                accessor: "tckn",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Soyisim",
                accessor: "surname",
                type: "text",
                operator: "=",
            },
            {
                label: "Departman",
                accessor: "department_name",
                type: "text",
                operator: "=",
            },
            {
                label: "Grev",
                accessor: "jobTitle",
                type: "select",
                operator: "=",
                selectData: this.state.jobTitlesData
            },
            {
                label: "Cinsiyet",
                accessor: "gender",
                type: "select",
                operator: "=",
                selectData: genderData()
            },
            {
                label: "e Balama Tarihi (Balang)",
                accessor: "min_employmentStartDate", //min_xxx MUST ALWAYS come before the max_xxx, if it exists.
                type: "date",
                operator: ">=",
            },
            {
                label: "e Balama Tarihi (Biti)",
                accessor: "max_employmentStartDate",
                type: "date",
                operator: "<=",
            },
            {
                label: "alma ekli",
                accessor: "employmentStatus",
                type: "text",
                operator: "=",
            },
            {
                label: "Adres",
                accessor: "address",
                type: "text",
                operator: "=",
            },
            {
                label: "Eitim Durumu",
                accessor: "educationLevel",
                type: "text",
                operator: "=",
            },
            {
                label: "Askerlik Durumu",
                accessor: "militaryStatus",
                type: "text",
                operator: "=",
            },
            {
                label: "Doum Tarihi (Balang)",
                accessor: "min_dateOfBirth", //min_xxx MUST ALWAYS come before the max_xxx, if it exists.
                type: "date",
                operator: ">=",
            },
            {
                label: "Doum Tarihi (Biti)",
                accessor: "max_dateOfBirth",
                type: "date",
                operator: "<=",
            },
            {
                label: "Doum Yeri",
                accessor: "cityOfBirth",
                type: "text",
                operator: "=",
            }
        ];
        let columnList = [
            {
                Header: (
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={e => this.setState({selection: e})}/>
                ),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           disabled={this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            },
            {
                Header: "Ad",
                accessor: "name",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.name} maxLength={15}/></div>)
            },
            {
                Header: "Soyad",
                accessor: "surname",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.surname} maxLength={15}/></div>)
            },
            {
                Header: "Departman",
                accessor: "department",
                Cell: row => (
                    <div style={{margin: 0}}>{row.department &&
                        <TextOverFlowTooltip text={row.department.name} maxLength={15}/>}</div>)
            },
            {
                Header: "Grev",
                accessor: "jobTitle",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.jobTitle} maxLength={15}/></div>)
            },
            {
                Header: "TCKN",
                accessor: "tckn"
            },
            {
                Header: "Departman",
                accessor: "expenditureDepartment",
                Cell: row => (
                    <div style={{margin: 0}}>{row.department &&
                        <TextOverFlowTooltip text={row.department.name} maxLength={15}/>}</div>)
            },
            {
                Header: "Durum",
                accessor: "statusType",
                Cell: row => (
                    <div>
                        <span>{statusTypeFilterData().map(function (e) {
                            if (e.value === row.statusType) return e.label
                        })}</span>
                    </div>)
            },
        ];
        let columns = renewListForDepartment(this, columnList, "accessor");
        let fields = renewListForDepartment(this, fieldList, "accessor");
        addCustomFields(this.state.titleFilterList, fieldList);
        return (
            <DefaultModal
                isOpen={this.props.modal}
                disabled={
                    this.state.btnDisabled ||
                    (this.state.fileList && this.state.fileList.length === 0) || (this.state.selection && this.state.selection.length === 0) ||
                    (this.props.newSession && !this.state.sessionMonth)}
                title="cret Dosyalarn Ykleme"
                size="lg"
                modalBodyStyle={{padding: "0", minHeight: "500px"}}
                secondaryButton={<button className="helpButton" onClick={() => {
                    this.activateJoyride()
                }} style={{position: "absolute", right: "70px", alignSelf: "center", marginTop: "-5px"}}>
                    <span className="tooltipText">Size yardmc olabilirim</span>
                    <i className="far fa-question-circle helpButtonIcon" style={{opacity: ".5"}}/></button>}
                confirmBtnText="Dokman(lar) Ykle"
                cancelBtnText="Kapat"
                onConfirm={(e) => this.sendNextBordroFile(this.state.index, true, 0)}
                onCancel={(e) => this.props.toggleModal(false, false)}>
                <Row>
                    {this.state.joyrideActive &&
                        <Joyride
                            disableBeacon={true}
                            autoStart
                            locale={locale}
                            steps={steps}
                            continuous={true}
                            run={this.state.shouldRun}
                            scrollToFirstStep={true}
                            showProgress={true}
                            showSkipButton={true}
                            scrollToSteps={true}
                            disableCloseOnEsc={true}
                            disableOverlay={false}
                            disableOverlayClicks={false}
                            keyboardNavigation={true}
                            hideBackButton={false}
                            disableOverlayClose={true}
                            callback={this.callback}
                            holePadding={0}
                            shouldRun={this.state.shouldRun}
                            styles={{
                                options: {
                                    zIndex: 100000,
                                    primaryColor: "#50a8e2"   // "#fc9636"
                                }
                            }}

                        />
                    }
                    {this.props.newSession &&
                        <div className=" col-sm-12 row" style={{marginTop: "15px"}}>
                            <div className="col-sm-4"/>
                            <div className="col-sm-8 row">
                                <div className="col-sm-4">
                                    Dnem
                                </div>
                                <div className="col-sm-4">
                                    <Datetime timeFormat={false} dateFormat="YYYY-MM" closeOnSelect={true}
                                              inputProps={{placeholder: 'YIL/AY'}}
                                              value={this.state.sessionMonth}
                                              renderMonth={this.props.renderMonth}
                                              isValidDate={function (current) {
                                                  return current.startOf('month').isSameOrBefore(Datetime.moment().startOf('month'));
                                              }}
                                              onChange={(e) => {
                                                  this.setState({sessionMonth: e});
                                                  this.props.onMonthChange(e)
                                              }}/>
                                </div>
                            </div>
                            <div className="col-sm-2"/>
                        </div>}
                    {this.props.newSession &&
                        <div className=" col-sm-12 row" style={{marginTop: "15px"}}>
                            <div className="col-sm-4"/>
                            <div className="col-sm-8 row">
                                <div className="col-sm-4">
                                    Lokasyon
                                </div>
                                <div className="col-sm-4">
                                    <Input type="text" name="location"
                                           value={this.state.location}
                                           onChange={this.changeLocation}/>
                                </div>
                            </div>
                            <div className="col-sm-2"/>
                        </div>}
                    <Col sm="12" style={{paddingTop: "15px", paddingRight: "32px", paddingLeft: "32px"}}>
                        <Dropzone
                            accept=".pdf"
                            activeClassName="dropzoneActiveClass"
                            className="FormRenk col-sm-12"
                            multiple={true}
                            disabled={this.state.btnDisabled}
                            onDrop={this.onDrop}>
                            <div className="dropzoneInnerArea">
                                cret dosyalarn (.pdf) ykleyiniz.
                            </div>
                        </Dropzone>
                    </Col>
                </Row>
                <Row>
                    <Col sm="12" style={{
                        paddingTop: "0px",
                        paddingRight: "15px",
                        paddingLeft: "15px",
                        maxHeight: '500px',
                        overflow: "auto"
                    }}>
                        <table className="" style={{textAlign: 'center', paddingBottom: "5px"}}>
                            <thead>
                            <tr style={{backgroundColor: '#ebedf0'}}>
                                <th width="5%">#</th>
                                <th>Dosya Ad</th>
                                <th>Dosya Boyutu</th>
                                <th>Durum</th>
                            </tr>
                            </thead>
                            <tbody>
                            {this.state.fileList && this.state.fileList.map((document, index) =>
                                <tr key={index + "_" + document.name + "_" + document.size}>
                                    <td>{index + 1}</td>
                                    <td style={{margin: "0px", textAlign: "left"}}>{<TextOverFlowTooltip
                                        text={document.name || "-"}
                                        maxLength={30}/>}</td>
                                    <td
                                        style={{margin: "0px"}}>{(new Decimal(new Decimal(document.size / 1024 / 1024).toFixed(2))).toNumber() + " MB"}</td>
                                    <td
                                        style={{margin: "0px"}}>{this.getStatusRender(document.status)}{(document.statusText || "Gnderim bekliyor")}</td>
                                </tr>
                            )}
                            {!this.state.processing && (this.state.fileList && this.state.fileList.length === 0) &&
                                <tr>
                                    <td colSpan="8">Kayt bulunamad</td>
                                </tr>
                            }
                            {this.state.processing &&
                                <tr>
                                    <td colSpan="8">Dokman ileniyor...</td>
                                </tr>
                            }
                            </tbody>
                        </table>
                    </Col>
                    <Col sm="12">
                        <p style={{float: "right"}}>Toplam
                            Adet: {this.state.fileList ? this.state.fileList.length : 0}</p>
                    </Col>
                </Row>
                <Row>
                    <Col sm="12">
                        <DetailedSearch fields={fields}
                                        loading={this.state.loading}
                                        onSearch={this.handleSearch}/>
                    </Col>
                </Row>
                <Row>
                    <Col sm="12">
                        <div className="col-xs-12" style={{marginTop: '20px'}}>
                            <Table columns={columns}
                                   content={this.state.content}
                                   loading={this.state.loading}
                                   total={this.state.total}
                                   page={this.state.page}
                                   manual={this.state.manual}
                                   size={this.state.size}
                                   options={["5", "10", "20", "50", "100"]}
                                   fetchData={this.fetchData}
                                   getTrStyles={this.getTrStyles}
                            />
                        </div>
                    </Col>
                </Row>


            </DefaultModal>
        );
    }
}

export default UploadBordroFileModalWithoutTckn;

