import React, {Component} from 'react';
import 'react-table/react-table.css'
import {Col, FormGroup, Label} from "reactstrap";
import Select from "../../../local_node_modules/react-select";
import DefaultModal from "../../../components/DefaultModal";
import Form from "react-bootstrap/Form";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import SweetAlert from "react-bootstrap-sweetalert";

class DocumentDeclineSpec extends Component {

    constructor(props) {
        super(props);
        this.state = {
            status: {},
            disableBtn: false
        };
    }

    componentDidMount() {
        this.setState({status: this.props.status ? {value: true, label: "Verilmi"} : {value: false, label: "Verilmemi"}});
    }

    saveClicked = () => {

        let self = this;
        let isChecked = this.state.status.value
        let declineList =  [];
        declineList.push(this.props.id);
        let params = {
            url: `/bordro/updateBordroDeclineStatus?isChecked=${isChecked}`,
            method: "post",
            data: {
                declineIdList: declineList,
                sessionId: this.props.sessionId
            }
        };
        self.setState({alert: null, disableBtn: true});
        requestK(params)
            .then(function (response) {
                self.setState({disableBtn: false});
                self.props.toggleModal(false, true);
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disableBtn: false});
        });
    };

    render() {
        return (
            <DefaultModal isOpen={this.props.modal}
                          size="sm" closeOnEsc={false}
                          title="Reddetme Hakk Atama"
                          confirmBtnText="Kaydet"
                          cancelBtnText="Kapat"
                          disabled={this.state.disableBtn}
                          onConfirm={() => this.setState({
                              alert: (
                                  <SweetAlert showCancel warning
                                              confirmBtnText="Evet"
                                              cancelBtnText="Hayr"
                                              confirmBtnBsStyle="danger"
                                              cancelBtnBsStyle="default"
                                              title="Emin misiniz?"
                                              onConfirm={() => this.saveClicked()}
                                              onCancel={() => this.setState({alert: null})}
                                              style={{top: '100%'}}
                                  >
                                      {this.state.status.value ? "Dokmana reddedebilme yetkisi verilecektir." : "Dokmandan reddedebilme yetkisi kaldrlacaktr."}
                                  </SweetAlert>
                              )
                          })}
                          onCancel={() => this.props.toggleModal(false, false)}>
                <Form>
                    <FormGroup row>
                        <Label sm={6}>Reddetme Hakk: </Label>
                        <Col sm={6}>
                            <Select value={this.state.status}
                                    clearable={false}
                                    disabled={this.state.disableBtn}
                                    options={[
                                        {value: true, label: 'Verilmi'},
                                        {value: false, label: 'Verilmemi'},
                                    ]}
                                    placeholder="Seiniz..."
                                    onChange={e => this.setState({status: e})}/>
                            {this.state.alert}
                        </Col>
                    </FormGroup>
                </Form>
            </DefaultModal>
        );
    }
}

export default DocumentDeclineSpec;