import React, {Component} from 'react';
import ReactTable from 'react-table'
import checkboxHOC from "react-table/lib/hoc/selectTable";
import 'react-table/react-table.css'
import ReactLoading from 'react-loading';
import {showAxiosError} from "../../../../helper/helper";
import {requestK} from "../../../../helper/APIUtils";
import {renewListForDepartment} from "../../common/PeykHelper";

const CheckboxTable = checkboxHOC(ReactTable);

class EmployeeSelector extends Component {

    constructor(props) {
        super(props);
        this.state = {
            employeeListLoading: false
        }
    }

    componentDidMount() {
        let self = this;
        let params = {
            url: '/employee/readActiveEmployeeAll?page=' + 0 + '&size=' + 100 + '&customFields=',
            method: "get",
        };
        self.setState({
            loading: true,
            searching: true,
            manual: self.manual === true ? false : self.manual,
            employeeListLoading: true
        });
        requestK(params)
        .then(function (response) {
            self.setState({
                total: response.data.pageList.totalElements,
                employeeList: response.data.pageList.content,
                loading: false,
                employeeListLoading: false
            });
        })
        .catch(function (error) {
            self.setState({loading: false, searching: false, employeeListLoading: false});
            showAxiosError(error);
        });
    }

    renderMainTable() {
        const checkboxProps = {
            keyField: "id",
            isSelected: (key) => {
                return this.props.value && this.props.value.id === key;
            },
            toggleSelection: (key) => {
                let employee = this.state.employeeList.find((e) => e.id === parseInt(key.replace("select-", "")));
                if (employee)
                    this.props.onChange(employee);
            },
            selectType: "radio",
            getTrProps: (s, r) => {
                const selected = r && this.props.value && this.props.value.id === r.original.id;
                return {
                    style: {
                        backgroundColor: selected ? "lightgreen" : "inherit"
                    }
                };
            }
        };

        let columnList = [
            {
                Header: "Ad",
                accessor: "name"
            },
            {
                Header: "Soyad",
                accessor: "surname"
            },
            {
                Header: "Departman",
                accessor: "department_name"
            },
            {
                Header: "Grevi",
                accessor: "jobtitle"
            },
            {
                Header: "TCKN",
                accessor: "tckn"
            },
            {
                Header: "KEP Adresi",
                accessor: "kepAddress"
            }
        ];
        let columns = renewListForDepartment(this, columnList, "accessor");

        return <CheckboxTable ref={r => (this.checkboxTable = r)} className="-striped -highlight"
                              data={this.state.employeeList} columns={columns} defaultPageSize={10}
                              previousText={"nceki"} nextText={"Sonraki"} pageText={"Sayfa"}
                              ofText={"Toplam:"} rowsText={"kayt"} {...checkboxProps} />
    }

    render() {
        return <div>
            {this.state.employeeListLoading && <ReactLoading type="spinningBubbles" style={{
                width: "150px",
                height: "150px",
                margin: "50px auto 0 auto",
                fill: "#888"
            }}/>}
            {!this.state.employeeListLoading && this.renderMainTable()}
        </div>
    }
}

export default EmployeeSelector;
