import React, {Component} from 'react';
import 'react-table/react-table.css'
import {Col, FormGroup, Input, Label} from "reactstrap";
import DefaultModal from "../../../../components/DefaultModal";
import Form from "react-bootstrap/Form";
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import SweetAlert from "react-bootstrap-sweetalert";

class ChangeLocation extends Component {

  constructor(props) {
    super(props);
    this.state = {
      location: "",
      disableBtn: false
    };
  }

  componentDidMount() {
    this.setState({location: this.props.location});
  }

  saveClicked = () => {

    let self = this;
    let params = {
      url: '/bordrosession/updateLocation',
      method: "post",
      data: {
        location: this.state.location,
        sessionId: this.props.sessionId
      }
    };
    self.setState({disableBtn: true, alert: null});
    requestK(params)
      .then(function (response) {
        self.setState({disableBtn: false});
        self.props.toggleModal(false, true);
      }).catch(function (error) {
      showAxiosError(error);
      self.setState({disableBtn: false});
    });
  };

  render() {
    return (
      <DefaultModal isOpen={this.props.modal}
                    size="sm" closeOnEsc={false}
                    title="Lokasyon Bilgisi Dzenle"
                    confirmBtnText="Kaydet"
                    cancelBtnText="Kapat"
                    disabled={this.state.disableBtn}
                    onConfirm={() => this.setState({
                      alert: (
                        <SweetAlert showCancel warning
                                    confirmBtnText="Evet"
                                    cancelBtnText="Hayr"
                                    confirmBtnBsStyle="danger"
                                    cancelBtnBsStyle="default"
                                    title="Emin misiniz?"
                                    onConfirm={() => this.saveClicked()}
                                    onCancel={() => this.setState({alert: null})}
                                    style={{top: '100%'}}
                        >
                          Lokasyon bilgisi deitirilecektir.
                        </SweetAlert>
                      )
                    })}
                    onCancel={() => this.props.toggleModal(false, false)}>
        <Form>
          <FormGroup row>
            <Label sm={6}>Lokasyon: </Label>
            <Col sm={6}>
              <Input type="text"
                     name="location"
                     id="location"
                     value={this.state.location}
                     onChange={e => this.setState({location: e.target ? e.target.value : ""})}/>
              {this.state.alert}
            </Col>
          </FormGroup>
        </Form>
      </DefaultModal>
    );
  }
}

export default ChangeLocation;
