
import React, {Component} from 'react';
import {Button, Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import EmployeeSelector from './EmployeeSelector'
import {showAxiosError} from "../../../../helper/helper";
import Alert from "react-s-alert";
import {requestK} from "../../../../helper/APIUtils";
import {isDepartmentMandatory} from "../../common/PeykHelper";

class BordroEmployeeEditor extends Component {

    constructor(props) {
        super(props);
        this.state = {
            alert: null
        };
        this.editClicked = this.editClicked.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.bordro !== prevState.bordro) {
            return {
                bordro: nextProps.bordro,
                selectedEmployee: undefined
            }
        }
        return null;
    }

    editClicked(e) {
        this.setState({edit: true});
    }

    saveClicked(e) {
        let self = this;
        let params = {
            url: `/bordro/updateBordroEmployee`,
            method: "post",
            data: {
                bordroId: self.props.bordro.id,
                employeeId: self.state.selectedEmployee.id
            }
        };
        self.setState({disabledSaveBtn:true});
        requestK(params)
        .then(function (response) {
            let message = response.data;
            self.setState({edit: false, alert: null,disabledSaveBtn:false});
            self.props.toggle && self.props.toggle();
            self.props.onBordroUpdated();
            Alert.success(message, {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledSaveBtn:false});
        });
    }

    renderView() {
        return (
            <div>
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-2">
                        <label>TCKN</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-10">
                        {this.props.bordro.employee &&
                        <span className="text-success">{this.props.bordro.employee.tckn}</span>}
                        {!this.props.bordro.employee &&
                        <span className="text-danger">{this.props.bordro.parsedTckn}</span>}
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-2">
                        <label>Ad</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-10">
                        <span>{this.props.bordro.employee && this.props.bordro.employee.name}</span>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-2">
                        <label>Soyad</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-10">
                        <span>{this.props.bordro.employee && this.props.bordro.employee.surname}</span>
                    </div>
                </div>
                {isDepartmentMandatory(this) &&
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-2">
                        <label>Departman</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-10">
                        <span>{this.props.bordro.employee && this.props.bordro.employee.department && this.props.bordro.employee.department.name}</span>
                    </div>
                </div>}
                {isDepartmentMandatory(this) &&
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-2">
                        <label>Grevi</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-10">
                        <span>{this.props.bordro.employee && this.props.bordro.employee.jobtitle}</span>
                    </div>
                </div>}
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-2">
                        <label>Kep Adresi</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-10">
                        <span>{this.props.bordro.employee && this.props.bordro.employee.kepAddress}</span>
                    </div>
                </div>
            </div>)
    }

    renderEdit() {
        return (
            <div>
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-3">
                        <label>cret Bordrosu TCKN</label>
                    </div>
                    <div className="col-sm-12 col-md-8 col-lg-9">
                        <span>{this.props.bordro.parsedTckn}</span>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12">
                        <EmployeeSelector value={this.state.selectedEmployee}
                                          onChange={(e) => this.setState({selectedEmployee: e})}/>
                    </div>
                </div>
            </div>)
    }

    render() {
        return <div>
            {this.state.alert}
            <Modal isOpen={this.props.bordro} size="lg" toggle={e => {
                this.setState({edit: false});
                this.props.toggle(e)
            }}>
                <ModalHeader className="justify-content-center" toggle={e => {
                    this.setState({edit: false});
                    this.props.toggle(e)
                }}>
                    cret Bordrosu alan Bilgileri
                </ModalHeader>
                <ModalBody>
                    {this.props.bordro && !this.state.edit && this.renderView()}
                    {this.props.bordro && this.state.edit && this.renderEdit()}
                </ModalBody>
                <ModalFooter>
                    <Button title="ptal" color="secondary" onClick={e => {
                        this.setState({edit: false});
                        this.props.toggle(e)
                    }}>
                        ptal
                    </Button>
                    {!this.state.edit && <Button title="alan Deitir" color="primary" onClick={this.editClicked}>
                        Dzenle
                    </Button>}
                    {this.state.edit &&
                    <Button title="Seili alan Kaydet" color="primary" disabled={!this.state.selectedEmployee || this.state.disabledSaveBtn}
                            onClick={this.saveClicked}>
                        Kaydet
                    </Button>}
                </ModalFooter>
            </Modal>
        </div>
    }
}

export default BordroEmployeeEditor;
