    zipAndUploadFiles = () => {
        let self = this;
        let fileList = self.state.fileList;
        const zip = require('jszip')();
        fileList.forEach(file => {
            zip.file(file.name, file.content);
        });
        zip.generateAsync({
            type: 'arrayBuffer',
            compression: 'DEFLATE',
            compressionOptions: {
                level: 9
            }
        }).then(content => {
            let file = {
                content:
                    new File(
                        [new Blob([content], {type: "application/zip"})],
                        "yuklenenDokumanlar.zip",
                        {type: "application/zip", lastModified: Date.now()}),
                name: "zipFile.zip",
                size: 1};
            fileList = [];
            fileList.push(file);
            let formData = new FormData();
            let methodCall;

            if (this.state.sessionToBeCreatedn) {
                formData.append("content", file.content);
                formData.append("year", self.state.sessionMonth.year().toString());
                formData.append("month", (self.state.sessionMonth.month() + 1).toString());
                formData.append("totalCount", 1);
                formData.append("location", !!self.state.location ? self.state.location : "");
                methodCall = "/create";

            } else {
                formData.append("content", file.content);
                formData.append("id", self.props.id ? self.props.id : self.state.id);
                formData.append("totalCount", 1);
                methodCall = "/sendNextBordroFile";
            }

            let params = {
                url: `/bordrosession${methodCall}`,
                method: 'post',
                cancelToken: self._source.token,
                data: formData
            };
            fileList[0] = {
                status: "processing",
                statusText: "Gnderim balatld.",
                name: file.name,
                size: file.size,
                content: file.content,
            };
            self.setState({btnDisabled: true, fileList});
            requestK(params)
                .then(function (response) {
                    let document = fileList[0];
                    fileList[0] = {
                        status: "success",
                        statusText: "Gnderim baarl.",
                        name: document.name,
                        size: document.size,
                        content: document.content,
                    };
                    self.setState({fileList, id: response.data, btnDisabled: false});
                    self.props.toggleModal(true, true);
                    if (this.state.sessionToBeCreated) {
                        self.props.toggleModal(false, true);
                        self.props.history.push(`/${self.props.peykName}/bordroimport/${self.state.id}`);
                        self.props.loadBordroSession();
                    }
                })
                .catch(function (error) {
                    if (!axios.isCancel(error)) {
                        file.status = "error";
                        file.statusText = error.data;
                        fileList[0] = {
                            status: "error",
                            statusText: error.data,
                            name: file.name,
                            size: file.size,
                            content: file.content,
                        };
                        self.setState({fileList: fileList, btnDisabled: false});
                        showAxiosError(error);
                    }
                });

        });
    };

