    checkSessionMonthIsUsed(selectedDate) {
        let loadedBordros = this.state.monthlyBordros;
        if (moment(selectedDate, "YYYY/MM", true).isValid() &&
            loadedBordros &&
            loadedBordros.some(function (x) {
                return (parseInt(moment(x.term, 'YYYY/MM/DD').format('M')) === parseInt(selectedDate.month()) + 1) &&
                    (parseInt(moment(x.term, 'YYYY/MM/DD').format('YYYY')) === parseInt(selectedDate.year()));
            })
        ) {
            this.setState({
                alert: (
                    <SweetAlert showCancel warning
                                confirmBtnText="Evet"
                                cancelBtnText="Hayr"
                                confirmBtnBsStyle="success"
                                cancelBtnBsStyle="default"
                                title="Emin misiniz?"
                                onConfirm={() => this.setState({alert: null})}
                                onCancel={() => this.setState({removeMonth: true, alert: null})}
                    >
                        Daha nce seilmi bir cret bordrosu dnemini setiniz.
                    </SweetAlert>
                )
            });
        }
    }

    renderMonth(props, month, year) {
        let loadedBordros = this.state.monthlyBordros;
        if (loadedBordros && loadedBordros.some(function (x) {
            return (parseInt(moment(x.term, 'YYYY/MM/DD').format('M')) === parseInt(month) + 1) &&
                (parseInt(moment(x.term, 'YYYY/MM/DD').format('YYYY')) === parseInt(year));
        })) {
            return <td {...props} bgcolor="#d3d3d3">{monthName(month + 1)}</td>;
        } else {
            return <td {...props} >{monthName(month + 1)}</td>;
        }

    }

