import React, {Component} from 'react';
import {customFieldsBooleanControl, showAxiosError} from "../../../../helper/helper";
import {requestK} from "../../../../helper/APIUtils";
import DefaultModal from "../../../../components/DefaultModal";
import Alert from "react-s-alert";
import {isValidEmployee} from "../../../../common/componentValidation";
import MaskedInput, {conformToMask} from "react-text-mask";
import {
    employmentContractData,
    genderData,
    getEmployees,
    isDepartmentMandatory,
    isKepAddressUnique,
    statusReasonData,
    statusTypeSetData
} from "../../common/PeykHelper";
import {Col, Form, FormGroup, Input, Label, Row, UncontrolledTooltip} from "reactstrap";
import Select from "../../../../local_node_modules/react-select";
import moment from "moment/moment";
import Datetime from "react-datetime";
import AutoCompleteList from "../../../../components/AutoCompleteList";
import SweetAlert from "react-bootstrap-sweetalert";
import NumberFormat from 'react-number-format';
import {SEE_SALARY_DATA} from "../../../../common/authorities/GrantDef";
import emailMask from "text-mask-addons/dist/emailMask";
import {validateField} from "../../../../common/validation";
import ReactLoading from "react-loading";

class CreateEditEmployeeModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            employee: {},
            error: {},
            departmentList: [],
            expenditureDepList: [],
            ntfTypeList: [],
            customFields: {},
            dateValidation: true,
            groupList: [],
            selectedGroupList: [],
            selectedEmpGroupList: [],
            jobTitles: [],
            jobTitle: {value: "", label: ""},
            isLoading: false,
            isDisabled: false,
        };
        this.handleChange = this.handleChange.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.validateClass = this.validateClass.bind(this);
        this.getNtfTypeList = this.getNtfTypeList.bind(this);
        this.onManagerChange = this.onManagerChange.bind(this);
        this.onSecondManagerChange = this.onSecondManagerChange.bind(this);
        this.onThirdManagerChange = this.onThirdManagerChange.bind(this);
        this.onDepartmentChange = this.onDepartmentChange.bind(this);
        this.handleChangeDouble = this.handleChangeDouble.bind(this);
        this.handleChangeInteger = this.handleChangeInteger.bind(this);
        this.onChangeNtfTypeList = this.onChangeNtfTypeList.bind(this);
        this.handleSalaryDouble = this.handleSalaryDouble.bind(this);
        this.getEmployeeGroupList = this.getEmployeeGroupList.bind(this);
        this.getEmployee = this.getEmployee.bind(this);
        this.isNumeric = this.isNumeric.bind(this);
        this.onChangeOnJobTitle = this.onChangeOnJobTitle.bind(this);
        CreateEditEmployeeModal.getTelephoneNumber = CreateEditEmployeeModal.getTelephoneNumber.bind(this);
        this.validateField = this.validateField.bind(this);
        this.validateMessage = this.validateMessage.bind(this);
    }

    validateField(e) {
        let { name, value } = e.target;
        let id = e.target.id;
        let rule = e.target.dataset.vdata;
        let boundary = e.target.dataset.vlength;
        const turkishCharacterPattern = /[]/g;
        if (name === "email") {
            if (value.match(turkishCharacterPattern)) {
                return;
            }
        }
        let result = validateField(rule, boundary, value);
        let error = this.state.error;
        error = Object.assign(error, {[id]: result});
        this.setState({error});
    }

    validateMessage(id) {
        let error = this.state.error;
        return (error[id] === undefined ? '' : (error[id].message));
    }

    static getDepartmentElement(data) {
        if (data.department) {
            return {value: data.department.id.toString(), label: data.department.name};
        } else {
            return null;
        }
    }

    static getExpenditureElement(data) {
        if (data.expenditureDepartment) {
            return {
                value: data.expenditureDepartment.id.toString(),
                label: data.expenditureDepartment.name + "-(" + data.expenditureDepartment.integrationCode + ")"
            };
        } else {
            return null;
        }
    }

    static getTelephoneNumber(telephone) {
        let phoneNumberMask = ['0', '(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];
        let conformedPhoneNumber = conformToMask(
            telephone,
            phoneNumberMask,
            {guide: false}
        );
        return conformedPhoneNumber.conformedValue;
    }

    componentWillMount() {
        if (isDepartmentMandatory(this)) {
            this.getEmployeeInfo();
        }
        if (!!this.props.id) {
            this.readCustomFields();
        }
    }

    getEmployeeInfo = () => {
        let self = this;
        let params = {
            url: '/employee/getList',
            method: "get",
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                let {departmentList, expenditureDepList, ntfTypeList} = response.data;
                self.setState({departmentList, ntfTypeList, expenditureDepList, loading: false});
            }).catch(function (error) {
            showAxiosError(error);
        })
            .finally(function () {
                self.setState({ loading: false });
            });
    };

    getGroupList = () => {
        let self = this;
        let params = {
            url: '/employee/getGroups',
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let groupList = [];
                response.data.map((group) => {
                    groupList.push({value: group.id, label: group.name, group: group})
                });
                self.setState({groupList});
                self.getEmployee();
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    getJobTitles = () => {
        let self = this;
        let params = {
            url: `/job/titles`,
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let jobTitles = response.data;
                self.setState({jobTitles});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    readCustomFields = () => {
        let self = this;
        let params = {
            url: `/employeecustom/readCustomFields`,
            method: "post",
            data: self.props.id
        };
        requestK(params)
            .then(function (response) {
                if (response.data && Object.keys(response.data) && Object.keys(response.data).length) {
                    self.setState({customFields: response.data});
                }
            })
            .catch(function (error) {
            });
    };

    componentDidMount() {
        let self = this;
        self.getGroupList();
        self.getJobTitles();
    }

    getEmployee() {
        let self = this;
        let params = {
            url: `/employee/getEmployeeById`,
            method: "post",
            data: self.props.id ? self.props.id : -1
        };
        requestK(params)
            .then(function (response) {
                if (!response.data.id && self.props.tckn) {
                    response.data.tckn = self.props.tckn;
                }
                let employee = response.data;
                if (!self.props.id) {
                    employee.statusType = self.props.isCandidate ? "CANDIDATE" : "ACTIVE";
                }
                self.setState({
                    employee,
                    jobTitle: {value: 1, label: employee.jobTitle},
                    manager: response.data.manager,
                    secondManager: response.data.secondManager,
                    thirdManager: response.data.thirdManager,
                    department: CreateEditEmployeeModal.getDepartmentElement(response.data),
                    expenditureDep: CreateEditEmployeeModal.getExpenditureElement(response.data)
                });
                self.getEmployeeGroupList();
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    handleChange(e) {
        let { name, value, type } = e.target;

        if (name === "name" || name === "surname") {
            let letter = /^[a-zA-Z\s]+$/;
            if (value.match(letter) || value.length === 0) {
                let employee = this.state.employee;
                employee[name] = value;
                this.setState({ employee });
            }
        } else if (name === "email") {
            const turkishCharacterPattern = /[]/g;
            let employee = this.state.employee;
            employee[name] = value;
            this.setState({ employee });

            if (value.match(turkishCharacterPattern)) {
                this.setState({ btnDisabled: true });
            } else {
                this.setState({ btnDisabled: false });
            }
        } else if (name === 'statusType') {
            let employee = this.state.employee;
            if (value !== "PASSIVE") {
                employee["passiveEndDate"] = null;
                employee["reason"] = null;
            }
            employee[name] = value;
            this.setState({ employee });
        } else {
            if (type === 'checkbox')
                value = e.target.checked;

            let employee = this.state.employee;
            employee[name] = value;
            this.setState({ employee });
        }
    }

    isNumeric(str) {
        if (typeof str != "string") return false
        return !isNaN(str) && !isNaN(parseFloat(str))
    }

    handleChangeDouble(e) {
        let self = this;
        let {name, value} = e.target;
        if (value.length > 1) {
            value = value.toString().replace(",", ".");
        }
        if (self.isNumeric(value) || value.length === 0) {
            let employee = self.state.employee;
            if (value && value.length === 1) {
                value = value.split(" ").join("");
                employee[name] = value;
                self.setState({employee});
            } else {
                employee[name] = value;
                self.setState({employee});
            }
        }
    }

    handleSalaryDouble(e) {
        let self = this;
        let value = e.value;
        if (!isNaN(parseFloat(value) || value.length === 0)) {
            let employee = self.state.employee;
            if (value && value.length === 1) {
                value = value.split(" ").join("");
                employee["salary"] = value;
                self.setState({employee});
            } else {
                employee["salary"] = value;
                self.setState({employee});
            }
        }
    }

    handleChangeInteger(e) {
        let {name, value} = e.target;
        if (name === "telephone" && value && value.length > 0) {
            value = value.split(" ").join("");
            value = value.replace(/[{()}]/g, '').replace(/-/g, "").replace(/_/g, "");
            if (value[0] === "0") {
                value = value.substring(1, value.length);
            }
        } else if (name === "telephone" && value && value.length === 0) {
            value = "0";
        }
        if (name === "telephone" && value.length > 10) {
            value = value.substring(0, 10);
        }
        if (!isNaN(value)) {
            let employee = this.state.employee;
            if (value && value.length === 1) {
                value = value.split(" ").join("");
                employee[name] = value;
                this.setState({employee});
            } else {
                employee[name] = value;
                this.setState({employee});
            }
        }
    }

    handleChangeCustomFields = (e, type) => {
        let {name, value} = e.target;
        if (type === "INTEGER") {
            let intFormat = /^\d+$/;
            if (value.length > 0 && !value.match(intFormat)) {
                return;
            }
        } else if (type === "DOUBLE") {
            let doubleFormat = /^[0-9.]+$/;
            if (value.length > 0 && !(value.match(doubleFormat) && value.toString().substring(value.toString().indexOf(".") + 1).indexOf(".") === -1)) {
                return;
            }
        }
        let customFields = this.state.customFields;
        customFields[name] = value;
        this.setState({customFields});
    };

    handleChangeCustomDoubleFields = (e, type, name) => {
        let value = e.value;
        let customFields = this.state.customFields;
        if (!isNaN(parseFloat(value) || value.length === 0)) {
            if (value && value.length === 1) {
                value = value.split(" ").join("");
                customFields[name] = value;
                this.setState({customFields});
            } else {
                customFields[name] = value;
                this.setState({customFields});
            }
        }
    };

    handleChangeCustomDateTimeFields = (name, value) => {
        let dateFormat = /^[0-9\s\-:]+$/;
        if (value.length > 0 && (!value.match(dateFormat) || !moment(value, "YYYY-MM-DD HH:mm:ss", true).isValid())) {
            this.setState({dateValidation: false});
            return;
        }
        if (value && value.toString().length) {
            value = value.format("YYYY-MM-DD HH:mm:ss").toString();
        }
        let customFields = this.state.customFields;
        customFields[name] = value;
        this.setState({customFields, dateValidation: true});
    };

    handleBooleanFields = (name, e) => {
        let customFields = this.state.customFields;
        customFields[name] = e.target.checked;
        this.setState({customFields});
    };

    validateClass(id) {
        let error = this.state.error;
        return (error[id] === undefined ? "" : (error[id].valid ? " has-success" : " has-danger"));
    }

    validateEmail(e) {
        let { value } = e.target;
        const turkishCharacterPattern = /[]/g;

        if (value.match(turkishCharacterPattern)) {
            let error = this.state.error;
            error["cEmail"] = {
                "message": "E-posta Trke karakter ieremez.",
                "valid": false
            };
            this.setState({ error });
            Alert.error('E-posta Trke karakter ieremez.', {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000,
                html: false
            });
        } else {
            let error = this.state.error;
            delete error["cEmail"];
            this.setState({ error });
        }
    }

    generateJobTitle = () => {
        let {employee, jobTitle} = this.state;
        employee.jobTitle = jobTitle.label;
        this.setState({employee});
    }
    isEmailSelected(notificationTypeList) {
        return Array.isArray(notificationTypeList) &&
            notificationTypeList.some(item => item.ntfType && item.ntfType.longCode === 'EMAIL');
    }

    handleSaveClick = () => {
        let { employee } = this.state;
        employee = this.setDepartmentInfo({ ...employee });

        // Telefon numaras kontrol
        if (!this.isPhoneNumberValid(employee.telephone)) {
            Alert.warning("Ltfen geerli bir telefon numaras giriniz.");
            return;
        }

        // T.C. kimlik no kontrol
        if (!this.isTCValid(employee.tckn)) {
            Alert.warning("Ltfen geerli bir T.C. Kimlik numaras giriniz.");
            return;
        }

        // Bildirim tr kontrol
        if (!this.isBildTuruSelected(employee.employeeNotificationTypeList)) {
            Alert.warning("Ltfen en az bir bildirim tr seiniz.");
            return;
        }

        const emailSelected = this.isEmailSelected(employee.employeeNotificationTypeList);
        if (emailSelected && (!employee.email || employee.email.trim() === '')) {
            Alert.warning("E-posta bildirimi seili iken e-Posta alan bo braklamaz.");
            return;
        }


        // Departman kontrol
        if (!this.isDepartmanSelected(employee.department)) {
            Alert.warning("Ltfen departman bilgisini giriniz.");
            return;
        }

        // Tm kontroller geti, imdi kayt balatlabilir
        this.setState({ isLoading: true, isDisabled: true }, () => {
            this.saveClicked();
        });
    };

    isDepartmanSelected(department) {
        return department && department.id;
    }

    isBildTuruSelected(notificationTypeList) {
        return Array.isArray(notificationTypeList) && notificationTypeList.length > 0;
    }

    isPhoneNumberValid(phone) {
        return phone && phone.length === 10;
    }

    isTCValid(tckn) {
        return tckn && tckn.length === 11;
    }


    saveClicked() {
        let self = this;
        this.generateJobTitle();
        let employee = self.setDepartmentInfo(self.state.employee);
        employee.employeeGroups = self.state.selectedEmpGroupList;
        employee = self.setExpenditureDepInfo(employee);
        let result = isValidEmployee(self, employee);
        let error = result.error;
        let valid = result.valid;
        if (!self.state.dateValidation) {
            valid = false;
            result = { errorMessage: "Girilen tarih bilgisi formata uygun deildir." };
        }

        if (valid) {
            let params = {
                url: `/employee/saveEmployee`,
                method: "post",
                data: {
                    employee: employee,
                    departmentMandatory: isDepartmentMandatory(self),
                    kepAddressUnique: isKepAddressUnique(self),
                    customFields: JSON.stringify(self.state.customFields),
                },
            };
            self.setState({ btnDisabled: true, isLoading: true });
            requestK(params)
                .then(function (response) {
                    let message = response.data;
                    self.setState({ btnDisabled: false, isLoading: false });
                    Alert.success(message, {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 5000,
                    });
                    if (!employee.id && self.props.checkIfRegistryManual) {
                        self.props.checkIfRegistryManual();
                    }
                    self.props.toggleModal(false, true);
                })
                .catch(function (error) {
                    showAxiosError(error);
                    self.setState({ btnDisabled: false, isLoading: false, isDisabled: false });
                });
        } else {
            Alert.error(result.errorMessage, {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 8000,
            });
            self.setState({ error: error, isDisabled: false });
        }
    }

    setDepartmentInfo(employee) {
        if (this.state.department) {
            employee.department = {id: parseInt(this.state.department.value), name: this.state.department.label};
        }
        return employee;
    }

    setExpenditureDepInfo(employee) {
        if (this.state.expenditureDep) {
            employee.expenditureDepartment = {
                id: parseInt(this.state.expenditureDep.value),
                name: this.state.expenditureDep.label
            };
        }
        return employee;
    }

    getNtfTypeList() {
        return this.state.employee && this.state.employee.employeeNotificationTypeList ? this.state.employee.employeeNotificationTypeList.map(ntfType => (JSON.stringify(ntfType.ntfType))) : [];
    }

    getEmployeeGroupList = () => {
        let self = this;
        let selectedGroupList = [];
        self.state.employee.employeeGroups.map(employeeGroup => (selectedGroupList.push({
            value: employeeGroup.group.id,
            label: employeeGroup.group.name,
            group: employeeGroup.group
        })));
        let selectedEmpGroupList = self.state.employee.employeeGroups;
        self.setState({selectedGroupList, selectedEmpGroupList});
    }

    onChangeNtfTypeList(e) {
        let result = e.map(ntfType => ({recordId: null, ntfType: JSON.parse(ntfType.value)}));
        let employee = this.state.employee;
        employee.employeeNotificationTypeList = result;
        this.setState({employee});
    }

    onChangeGroupList(e) {
        let self = this;
        let employee = self.state.employee;
        let selectedGroupList = [];
        let selectedEmpGroupList = [];
        e.forEach(selected => {
            let existEmpGroup = employee?.employeeGroups?.find(empGroup => (empGroup.group.id === selected.group.id));
            if (existEmpGroup) {
                selectedEmpGroupList.push(existEmpGroup);
            } else {
                selectedEmpGroupList.push({id: null, "group": selected.group})
            }
        });

        selectedEmpGroupList.map(selected => (selectedGroupList.push({
            value: selected.group.id,
            label: selected.group.name,
            group: selected.group
        })));
        self.setState({selectedGroupList, selectedEmpGroupList});
    }

    onExpenditureDepChange(dept) {
        if (dept) {
            this.setState({expenditureDep: dept});
        } else {
            this.setState({expenditureDep: {value: null, label: null}});
        }
    }

    onDepartmentChange(dept) {
        if (this.state.manager && this.state.manager.value) {
            this.setState({
                alert: (
                    <SweetAlert warning
                                confirmBtnText="Tamam"
                                confirmBtnBsStyle="error"
                                title="Uyar"
                                onConfirm={() => {
                                    this.setState({alert: null});
                                }
                                }>
                        Ynetici bilgisini kontrol edin!
                    </SweetAlert>
                )
            });
        }
        if (dept) {
            this.setState({department: dept});
        } else {
            this.setState({department: {value: null, label: null}});
        }
    }

    onManagerChange(data) {
        this.handleChange({
            target: {
                name: 'parentUserId',
                value: data && data.value
            }
        });
        this.setState({manager: data})
    }

    onSecondManagerChange(data) {
        this.handleChange({
            target: {
                name: 'secondParentUserId',
                value: data && data.value
            }
        });
        this.setState({secondManager: data})
    }
    onThirdManagerChange(data) {
        this.handleChange({
            target: {
                name: 'thirdParentUserId',
                value: data && data.value
            }
        });
        this.setState({thirdManager: data})
    }
    onChangeOnJobTitle(jobTitle) {
        if (jobTitle) {
            this.setState({jobTitle: jobTitle});
        } else {
            this.setState({jobTitle: {value: 0, label: ""}});
        }
    }

    hasGrant = (grant) => {
        return this.props.hasGrant(grant);
    };

    render() {
        return <DefaultModal
            isOpen={this.props.modal}
            hasGrant={this.props.hasGrant}
            fade={false}
            size="lg"
            closeOnEsc={false}
            title="alan Ekle/Dzenle Ekran"
            confirmBtnText={
                <span>
                    {this.state.isLoading ? (
                        <>
                            KAYDEDLYOR
                            <span className="spinner"></span>
                        </>
                    ) : (
                        "KAYDET"
                    )}
                </span>
            }
            confirmButtonStyle={{
                cursor: this.state.isLoading ? "not-allowed" : "pointer",
                opacity: this.state.isLoading ? 0.6 : 1,
            }}
            cancelBtnText="Kapat"
            onConfirm={!this.state.isLoading ? this.handleSaveClick : null}
            onCancel={() => this.props.toggleModal(false, false)}
        >
            <div style={{marginTop: '20px'}}>
                <Form>
                    <div className="FormRenk" style={{marginBottom: '10px', paddingLeft: '20px', paddingRight: '20px'}}>
                        <label style={{fontWeight: 'bold', display: 'block'}}>
                            Genel:
                        </label>
                        {this.renderInput("Ad *", "name")}
                        {this.renderInput("Soyad *", "surname")}
                        {this.renderInputInteger("TCKN *", "tckn", 11, this.props.id !== null)}

                        {isDepartmentMandatory(this) &&
                            <FormGroup row className={this.validateClass("department")}>
                                <Label sm={4}>Departman *</Label>
                                <Col sm={8}>
                                    <AutoCompleteList name={"Departman"}
                                                      useUpperCase={false} onlyLabel={false} allowNew={false}
                                                      list={this.state.departmentList}
                                                      value={this.state.department}
                                                      onChange={e => this.onDepartmentChange(e)}
                                                      updateList={e => this.setState({departmentList: e})}
                                                      placeholder={"Departman Seiniz veya Departman Ad Yaznz..."}
                                    />
                                </Col>
                            </FormGroup>
                        }
                        {isDepartmentMandatory(this) &&
                            <FormGroup row className={this.validateClass("jobTitle")}>
                                <Label sm={4}>{"Grev"}</Label>
                                <Col sm={8}>
                                    <AutoCompleteList name={"Grev"}
                                                      useUpperCase={false} onlyLabel={false} allowNew={true}
                                                      list={this.state.jobTitles}
                                                      value={this.state.jobTitle}
                                                      onChange={e => this.onChangeOnJobTitle(e)}
                                                      updateList={e => this.setState({jobTitles: e})}
                                                      placeholder={"Grev Seiniz veya Grev Ad Yaznz..."}
                                    />
                                </Col>
                            </FormGroup>
                        }

                        {isDepartmentMandatory(this) &&
                            <FormGroup row className={this.validateClass("manager")}>
                                <Label sm={4}>Bal Olduu Ynetici</Label>
                                <Col sm={8}>
                                    <Select.Async multi={false}
                                                  ignoreAccents={false}
                                                  value={this.state.manager}
                                                  onChange={(e) => this.onManagerChange(e)}
                                                  isLoading={true}
                                                  autoload={false}
                                                  cache={false}
                                                  clearable={true}
                                                  filterOptions={(options) => options}
                                                  noResultsText="Kayt bulunamad."
                                                  searchPromptText="Arama iin en az 2 karakter giriniz."
                                                  placeholder="sim veya soyisim ile ara..."
                                                  loadingPlaceholder="Ykleniyor..."
                                                  clearValueText="Seimi kaldr"
                                                  loadOptions={getEmployees}
                                                  disabled={this.state.isDisabled} />
                                </Col>
                            </FormGroup>
                        }
                        {isDepartmentMandatory(this) &&
                            <FormGroup row className={this.validateClass("manager")}>
                                <Label sm={4}>Bal Olduu 2. Ynetici</Label>
                                <Col sm={8}>
                                    <Select.Async multi={false}
                                                  ignoreAccents={false}
                                                  value={this.state.secondManager}
                                                  onChange={(e) => this.onSecondManagerChange(e)}
                                                  isLoading={true}
                                                  autoload={false}
                                                  cache={false}
                                                  clearable={true}
                                                  filterOptions={(options) => options}
                                                  noResultsText="Kayt bulunamad."
                                                  searchPromptText="Arama iin en az 2 karakter giriniz."
                                                  placeholder="sim veya soyisim ile ara..."
                                                  loadingPlaceholder="Ykleniyor..."
                                                  clearValueText="Seimi kaldr"
                                                  loadOptions={getEmployees}
                                                  disabled={this.state.isDisabled} />
                                </Col>
                            </FormGroup>
                        }
                        {isDepartmentMandatory(this) &&
                            <FormGroup row className={this.validateClass("manager")}>
                                <Label sm={4}>Bal Olduu 3. Ynetici</Label>
                                <Col sm={8}>
                                    <Select.Async multi={false}
                                                  ignoreAccents={false}
                                                  value={this.state.thirdManager}
                                                  onChange={(e) => this.onThirdManagerChange(e)}
                                                  isLoading={true}
                                                  autoload={false}
                                                  cache={false}
                                                  clearable={true}
                                                  filterOptions={(options) => options}
                                                  noResultsText="Kayt bulunamad."
                                                  searchPromptText="Arama iin en az 2 karakter giriniz."
                                                  placeholder="sim veya soyisim ile ara..."
                                                  loadingPlaceholder="Ykleniyor..."
                                                  clearValueText="Seimi kaldr"
                                                  loadOptions={getEmployees}
                                                  disabled={this.state.isDisabled} />
                                </Col>
                            </FormGroup>
                        }
                        {this.state.loading && (
                            <ReactLoading
                                type="spinningBubbles"
                                style={{width: "60px", height: "60px", margin: "20px auto 0 auto", fill: "#888"}}/>)}
                        {!this.props.isKepOwner &&
                            <FormGroup row className={this.validateClass("employeeNtfResultList")}>
                                <Label sm={4}>Bildirim Trleri *</Label>
                                <Col sm={8}>
                                    <Select
                                        className="select-control"
                                        name="employeeNtfResultList"
                                        id="employeeNtfResultList"
                                        multi={true}
                                        placeholder="Seiniz..."
                                        noResultsText={"Veri yok"}
                                        value={this.getNtfTypeList()}
                                        onChange={(e) => e && this.onChangeNtfTypeList(e)}
                                        options={this.state.ntfTypeList && this.state.ntfTypeList.map(ntfType => ({
                                            value: JSON.stringify(ntfType),
                                            label: ntfType.longCode
                                        }))}
                                        disabled={this.state.isDisabled}
                                    />
                                </Col>
                            </FormGroup>}
                        {this.props.isKepOwner &&
                            this.renderInput("KEP Adresi *", "kepAddress")}
                        <FormGroup row className={this.validateClass("telephone")}>
                            <Label sm={4}>Telefon Numaras *</Label>
                            <Col sm={8}>

                                <MaskedInput
                                    className="form-control"
                                    mask={['0', '(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
                                    style={{textAlign: "left"}}
                                    type="text"
                                    placeholder="(5xx) xxx-xxxx"
                                    name="telephone"
                                    value={this.state.employee.telephone}
                                    size="10"
                                    onChange={(e) => this.handleChangeInteger(e)}
                                    disabled={this.state.isDisabled}
                                />
                            </Col>
                        </FormGroup>

                        <FormGroup row className={this.validateClass("cEmail")}>
                            <Label sm={4}>{this.getNtfTypeList().filter(v => v.includes('EMAIL')).length === 1  ? 'e-Posta' + ' *' : 'e-Posta'}</Label>
                            <Col sm={8}>
                                <MaskedInput
                                    className="form-control"
                                    mask={emailMask}
                                    placeholder="_________@______.___"
                                    type="text"
                                    name="email"
                                    inputProps={{ vdata: "email", vlength: '1,255' }}
                                    id="cEmail"
                                    ref="cEmail"
                                    onBlur={(e) => this.validateEmail(e)}
                                    value={this.state.employee.email}
                                    onChange={(e) => this.handleChange(e)}
                                    disabled={this.state.isDisabled}
                                />
                                {this.validateMessage('cEmail') !== "" &&
                                    <UncontrolledTooltip placement="right" target="cEmail" delay={0}>
                                        {this.validateMessage("cEmail")}
                                    </UncontrolledTooltip>}
                            </Col>
                        </FormGroup>

                        <FormGroup row className={this.validateClass("statusType")}>
                            <Label sm={4}>Durumu</Label>
                            <Col sm={8}>
                                <Select
                                    placeholder="Seiniz..."
                                    value={this.state.employee.statusType ? this.state.employee.statusType : 'ACTIVE'}
                                    clearable={false}
                                    options={statusTypeSetData()}
                                    noResultsText={"Veri yok"}
                                    onChange={(e) => {
                                        this.handleChange({
                                            target: {
                                                name: 'statusType',
                                                value: (e && e.value) ? e.value : 'ACTIVE'
                                            }, e
                                        });
                                    }}
                                    disabled={this.state.isDisabled}
                                />
                            </Col>
                        </FormGroup>
                        {this.renderInput("Entegrasyon Kodu", "integrationCode")}
                        <FormGroup row className={this.validateClass("employmentContract")}>
                            <Label sm={4}> Szlemesi Tr</Label>
                            <Col sm={8}>
                                <Select value={this.state.employee.employmentContract}
                                        clearable={false}
                                        options={employmentContractData()}
                                        placeholder="Seiniz..."
                                        noResultsText={"Veri yok"}
                                        onChange={(e) => {
                                            this.handleChange({
                                                target: {
                                                    name: "employmentContract",
                                                    value: (e && e.value) ? e.value : 'UNDECLARED'
                                                }, e
                                            });
                                        }}
                                        disabled={this.state.isDisabled}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row className={this.validateClass("salary")}>
                            <Label sm={4}>cret</Label>
                            {this.hasGrant(SEE_SALARY_DATA) ?
                                <Col sm={8}>
                                    <NumberFormat
                                        placeholder="0,00"
                                        isNumericString={true}
                                        value={this.state.employee.salary}
                                        onValueChange={(e) => this.handleSalaryDouble(e)}
                                        sm={12}
                                        suffix={' '}
                                        style={{textAlign: 'right'}}
                                        thousandSeparator={'.'} decimalSeparator={','}
                                        displayType="input"
                                        allowNegative={false}
                                        decimalScale={2}
                                        disabled={this.state.isDisabled}
                                    />
                                </Col> :
                                <Col sm={8}>
                                    <Input type="text" name={"salary"}
                                           value={"********"}
                                    />
                                </Col>
                            }
                        </FormGroup>
                        <FormGroup row className={this.validateClass("vip")}>
                            <Label sm={4}>VIP</Label>
                            <Col sm={8}>
                                <input type="checkbox" name="vip"
                                       checked={this.state.employee.vip}
                                       onChange={(e) => this.handleChange(e)}
                                       disabled={this.state.isDisabled}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row className={this.validateClass("expenditure")}>
                            <Label sm={4}>Masraf Yeri </Label>
                            <Col sm={8}>
                                <AutoCompleteList name={"Masraf Yeri"}
                                                  useUpperCase={false} onlyLabel={false} allowNew={false}
                                                  list={this.state.expenditureDepList}
                                                  value={this.state.expenditureDep}
                                                  onChange={e => this.onExpenditureDepChange(e)}
                                                  updateList={e => this.setState({expenditureDepList: e})}
                                                  placeholder={"Masraf Yeri Seiniz veya Masraf Yeri Ad Yaznz..."}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row className={this.validateClass("groups")}>
                            <Label sm={4}>Gruplar</Label>
                            <Col sm={8}>
                                <Select
                                    className="select-control"
                                    name="groups"
                                    id="groups"
                                    multi={true}
                                    placeholder="Seiniz..."
                                    value={this.state.selectedGroupList}
                                    onChange={(e) => e && this.onChangeGroupList(e)}
                                    options={this.state.groupList}
                                    noResultsText={"Henz kaydedilmi veri yok"}
                                    disabled={this.state.isDisabled}
                                />
                            </Col>
                        </FormGroup>
                        {this.state.employee.statusType === 'PASSIVE' &&
                            <FormGroup row className={this.validateClass("passiveEndDate")}>
                                <Label sm={4}>Pasif Durumunun Bitecei Tarih</Label>
                                <Col sm={8}>
                                    <Datetime locale="tr"
                                              className="datepicker"
                                              input={true}
                                              closeOnSelect={true}
                                              inputProps={{disabled: this.state.employee.statusType !== 'PASSIVE' || this.state.isDisabled}}
                                              value={
                                                  moment(this.state.employee.passiveEndDate, "YYYY-MM-DD", true).isValid() ?
                                                      moment(this.state.employee.passiveEndDate).format("DD-MM-YYYY") :
                                                      this.state.employee.passiveEndDate
                                              }
                                              isValidDate={function (current) {
                                                  return current.startOf('date').isAfter(Datetime.moment().startOf('date'));
                                              }}
                                              dateFormat="DD-MM-YYYY"
                                              timeFormat={false}
                                              onChange={e => {
                                                  this.handleChange({
                                                      target: {
                                                          name: 'passiveEndDate',
                                                          value: (e && moment(e).isValid()) ? e.format("YYYY-MM-DD") : null
                                                      }
                                                  });
                                              }}
                                    />
                                </Col>
                            </FormGroup>
                        }
                        {this.state.employee.statusType === 'PASSIVE' &&
                            <FormGroup row className={this.validateClass("reason")}>
                                <Label sm={4}>Pasif Durumun Gerekesi</Label>
                                <Col sm={8}>
                                    <Select value={this.state.employee.reason}
                                            clearable={false}
                                            disabled={this.state.employee.statusType !== 'PASSIVE' || this.state.isDisabled}
                                            options={statusReasonData()}
                                            placeholder="Seiniz..."
                                            onChange={(e) => {
                                                this.handleChange({
                                                    target: {
                                                        name: "reason",
                                                        value: (e && e.value) ? e.value : null
                                                    }
                                                });
                                            }}
                                    />
                                </Col>
                            </FormGroup>
                        }
                    </div>
                    <div className="FormRenk" style={{paddingLeft: '20px', paddingRight: '20px'}}>
                        <label style={{fontWeight: 'bold', display: 'block'}}>
                            Detay:
                        </label>
                        <Row>
                            <Col sm={6}>
                                <FormGroup row className={this.validateClass("employmentStartDate")}
                                           style={{marginTop: '-2px'}}>
                                    <Label sm={4} style={{fontSize: '11px'}}>e Balama Tarihi</Label>
                                    <Col sm={8}>
                                        <Datetime locale="tr"
                                                  className="datepicker"
                                                  input={true}
                                                  closeOnSelect={true}
                                                  value={
                                                      moment(this.state.employee.employmentStartDate, "YYYY-MM-DD", true).isValid() ?
                                                          moment(this.state.employee.employmentStartDate).format("DD-MM-YYYY") :
                                                          this.state.employee.employmentStartDate
                                                  }
                                                  dateFormat="DD-MM-YYYY"
                                                  timeFormat={false}
                                                  onChange={e => {
                                                      this.handleChange({
                                                          target: {
                                                              name: 'employmentStartDate',
                                                              value: (e && moment(e, "YYYY-MM-DD", true).isValid()) ? moment(e).format("YYYY-MM-DD") : null
                                                          }
                                                      });
                                                  }}
                                                  disabled={this.state.isDisabled}
                                        />
                                    </Col>
                                </FormGroup>
                            </Col>
                            {this.renderInputSmaller("alma ekli", "employmentStatus")}
                        </Row>
                        <Row>
                            {this.renderInputSmallerDouble("Yllk zin Hakk", "remainingAnnualVacation", 5)}
                            {this.renderInputSmallerDouble("Mazeret zin Hakk", "remainingExcuseVacation", 5)}
                        </Row>
                        <Row>
                            <Col sm={6}>
                                <FormGroup row className={this.validateClass("gender")}>
                                    <Label sm={4} style={{fontSize: '11px'}}>Cinsiyet</Label>
                                    <Col sm={8}>
                                        <Select value={this.state.employee.gender}
                                                clearable={false}
                                                options={genderData()}
                                                placeholder="Seiniz..."
                                                noResultsText={"Veri Yok"}
                                                onChange={(e) => {
                                                    this.handleChange({
                                                        target: {
                                                            name: "gender",
                                                            value: (e && e.value && e.value !== ' ') ? e.value : null
                                                        }
                                                    });
                                                }}
                                                disabled={this.state.isDisabled}
                                        />
                                    </Col>
                                </FormGroup>
                            </Col>
                            {this.renderInputSmaller("Adres", "address")}
                        </Row>
                        <Row>
                            <Col sm={6}>
                                <FormGroup row className={this.validateClass("dateOfBirth")}
                                           style={{marginTop: '-2px'}}>
                                    <Label sm={4} style={{fontSize: '11px'}}>Doum Tarihi</Label>
                                    <Col sm={8}>
                                        <Datetime locale="tr"
                                                  className="datepicker"
                                                  input={true}
                                                  closeOnSelect={true}
                                                  value={
                                                      moment(this.state.employee.dateOfBirth, "YYYY-MM-DD", true).isValid() ?
                                                          moment(this.state.employee.dateOfBirth).format("DD-MM-YYYY") :
                                                          this.state.employee.dateOfBirth
                                                  }
                                                  dateFormat="DD-MM-YYYY"
                                                  timeFormat={false}
                                                  onChange={e => {
                                                      this.handleChange({
                                                          target: {
                                                              name: 'dateOfBirth',
                                                              value: (e && moment(e, "YYYY-MM-DD", true).isValid()) ? moment(e).format("YYYY-MM-DD") : null
                                                          }
                                                      });
                                                  }}
                                                  disabled={this.state.isDisabled}
                                        />
                                    </Col>
                                </FormGroup>
                            </Col>
                            {this.renderInputSmaller("Doum Yeri", "cityOfBirth")}
                        </Row>
                        <Row>
                            {this.renderInputSmaller("Eitim Durumu", "educationLevel")}
                            {this.renderInputSmaller("Askerlik Durumu", "militaryStatus")}
                        </Row>
                        {this.props.titleList && !!this.props.titleList.length &&
                            this.renderCustomFields()}
                    </div>
                </Form>
                {this.state.alert}
            </div>
        </DefaultModal>
    }

    renderCustomFields() {
        let prevKey = "";

        return this.props.titleList.map((key, index) => {
            if (index % 2 === 1) {
                return (
                    <Row>
                        {this.renderCustomInputSmaller(prevKey.title, prevKey.type, this.state.customFields[prevKey.title])}
                        {this.renderCustomInputSmaller(key.title, key.type, this.state.customFields[key.title])}
                    </Row>
                );
            } else if (index === (this.props.titleList.length - 1)) {
                return (
                    <Row>
                        {this.renderCustomInputSmaller(key.title, key.type, this.state.customFields[key.title])}
                    </Row>
                );
            } else {
                prevKey = key;
            }
        })
    }

    renderInput(label, fieldName) {
        return (
            <FormGroup row className={this.validateClass(fieldName)}>
                <Label sm={4}>{label}</Label>
                <Col sm={8}>
                    <Input
                        type="text"
                        name={fieldName}
                        value={this.state.employee[fieldName] || ''}
                        onChange={(e) => this.handleChange(e)}
                        disabled={this.state.isDisabled}
                    />
                </Col>
            </FormGroup>
        );
    }

    renderInputInteger(label, fieldName, maxLength, readOnly = false) {
        return (
            <FormGroup row className={this.validateClass(fieldName)}>
                <Label sm={4}>{label}</Label>
                <Col sm={8}>
                    <Input
                        type="text"
                        name={fieldName}
                        maxLength={maxLength}
                        disabled={this.state.isDisabled || readOnly}
                        value={this.state.employee[fieldName] || ''}
                        onChange={(e) => {
                            this.handleChangeInteger(e)
                        }}
                    />
                </Col>
            </FormGroup>
        );
    }

    renderInputSmaller(label, fieldName) {
        return <Col sm={6}>
            <FormGroup row className={this.validateClass(fieldName)}>
                <Label sm={4} style={{fontSize: '11px'}}>{label}</Label>
                <Col sm={8}>
                    <Input type="text" name={fieldName}
                           value={this.state.employee[fieldName] || ''}
                           onChange={(e) => {
                               this.handleChange(e)
                           }}
                           disabled={this.state.isDisabled}
                    />
                </Col>
            </FormGroup>
        </Col>;
    }

    renderInputSmallerDouble(label, fieldName, maxLength) {
        return <Col sm={6}>
            <FormGroup row className={this.validateClass(fieldName)}>
                <Label sm={4} style={{fontSize: '11px'}}>{label}</Label>
                <Col sm={8}>
                    <Input type="text" name={fieldName} maxLength={maxLength}
                           value={this.state.employee[fieldName] || ''}
                           onChange={(e) => {
                               this.handleChangeDouble(e)
                           }}
                           disabled={this.state.isDisabled}
                    />
                </Col>
            </FormGroup>
        </Col>;
    }

    renderCustomInputSmaller(label, type, value) {
        return <Col sm={6}>
            <FormGroup row>
                <Label sm={4} style={{fontSize: '11px'}}>{label}</Label>
                <Col sm={8}>{type !== "DATETIME" && type !== "BOOLEAN" && type !== "DOUBLE" &&
                    <Input type="text" name={label}
                           value={value || ''}
                           onChange={(e) => {
                               this.handleChangeCustomFields(e, type)
                           }}
                           disabled={this.state.isDisabled}
                    />}
                    {type === "DATETIME" &&
                        <Datetime locale="tr"
                                  className="datepicker"
                                  input={true}
                                  closeOnSelect={true}
                                  value={moment(value, "YYYY-MM-DD HH:mm:ss", true).isValid() ? moment(value).format("DD-MM-YYYY HH:mm:ss") : value}
                                  dateFormat="DD-MM-YYYY"
                                  timeFormat="HH:mm:ss"
                                  onChange={(e) => this.handleChangeCustomDateTimeFields(label, e)}
                                  disabled={this.state.isDisabled}
                        />}
                    {type === "BOOLEAN" &&
                        <Label check>
                            <Input type="checkbox" checked={customFieldsBooleanControl(value)}
                                   onClick={(e) => this.handleBooleanFields(label, e)}
                                   disabled={this.state.isDisabled}
                            />
                        </Label>
                    }
                    {type === "DOUBLE" &&
                        <NumberFormat
                            placeholder="0,00"
                            isNumericString={true}
                            value={value}
                            onValueChange={(e) =>this.handleChangeCustomDoubleFields(e, type,label)}
                            sm={12}
                            style={{textAlign: 'right'}}
                            thousandSeparator={'.'}
                            decimalSeparator={','}
                            displayType="input"
                            allowNegative={true}
                            name={label}
                            id={"numberFormat-component"}
                            className="form-control"
                            disabled={this.state.isDisabled}
                        />
                    }
                </Col>
            </FormGroup>
        </Col>;
    }
}

export default CreateEditEmployeeModal;
