import React, {Component} from 'react';
import Table from "../../../../components/Table/Table";
import {Container} from 'reactstrap';
import 'react-datetime/css/react-datetime.css'
import 'moment/locale/tr'
import moment from 'moment';
import {requestK} from "../../../../helper/APIUtils";
import {checkGrantToDisplay, showAxiosError} from "../../../../helper/helper";
import {DOWNLOAD_UPLOADED_PAYROLL_FILE} from "../../../../common/authorities/GrantDef";

class BordroFileTable extends Component {

    constructor() {
        super();

        this.state = {
            loading: true,
            fileCount: 0
        };

        this.load = this.load.bind(this);
        this.downloadSessionFile = this.downloadSessionFile.bind(this);
    }

    componentDidMount() {
        this.load();
    }

    load() {
        if (this.props.bordroSession && this.props.bordroSession.id) {
            let self = this;
            let params = {
                url: `/bordrofile/readBordroFileAll`,
                method: "post",
                data: this.props.bordroSession.id
            };
            self.setState({loading: true});
            requestK(params)
                .then(function (response) {
                    self.setState({
                        fileList: response.data,
                        fileCount: (response.data && response.data.length) ? response.data.length : 0,
                        loading: false
                    });
                }).catch(function (error) {
                showAxiosError(error);
                self.setState({loading: false});
            });
        }
    }

    downloadSessionFile(file) {
        let self = this;
        let params = {
            url: `/bordrofile/downloadBordroFile`,
            responseType: 'arraybuffer',
            method: "post",
            data: file.id
        };
        self.setState({loading: true});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], {type: "application/pdf"});
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(blob);
                link.download = file.loadName;
                document.body.appendChild(link);
                link.click();
                self.setState({loading: false});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({loading: false});
        });
    }

    render() {
        let fileList = this.state.fileList;
        let columns = [
            {
                Header: "Dosya Tr",
                accessor: "bulkBordroType"
            },
            {
                Header: "Dosya ad",
                accessor: "loadName"
            },
            {
                Header: "Ykleme Zaman",
                accessor: "createdAt",
                Cell: row => (
                    <div>{moment(row.createdAt).isValid() ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}</div>)
            },
            {
                Header: "Hata Durumu",
                accessor: "error"
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                Cell: row => (
                    <div style={{textAlign: 'left'}}>
                        {(row.error === undefined || row.error === null) && checkGrantToDisplay(this, DOWNLOAD_UPLOADED_PAYROLL_FILE) &&
                            <button title="Dosyay ndir" className="btn-icon btn btn-linkedin btn-sm"
                                    style={{margin: '0px 3px'}}
                                    onClick={e => {
                                        this.downloadSessionFile(row)
                                    }}>
                                <i className="BtnIcon fa fa-download"/>
                            </button>
                        }
                    </div>
                )
            }
        ];

        return (
            <Container>
                <div>
                    <Table columns={columns}
                           content={fileList ? fileList : []}
                           loading={this.state.loading}
                           total={this.state.fileCount}
                           page={0}
                           manual={this.state.manual}
                           size={20}/>
                </div>
            </Container>)
    }
}

export default BordroFileTable;
