import React, {Component} from 'react';
import Table from "../../../../components/Table/Table";
import {Col, Input, Label} from 'reactstrap';
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import SweetAlert from 'react-bootstrap-sweetalert';
import 'react-datetime/css/react-datetime.css'
import 'moment/locale/tr'
import moment from 'moment';
import {authorizationWays, showAxiosError} from "../../../../helper/helper";
import DropdownCheck from "../../../../components/Table/DropdownCheck";
import {requestK} from "../../../../helper/APIUtils";
import Alert from "react-s-alert";
import {
    downloadApproveDocument,
    genderData,
    getJobTitles,
    renewListForPrivileges,
    stateCompare,
    stateData,
    statusTypeFilterData
} from "../../common/PeykHelper";
import DocumentState from "../../common/DocumentState";
import DepartmentSelectionTree from "../../organization/DepartmentSelectionTree";
import StateDetail from "../../common/StateDetail";
import BordroEmployeeEditor from "./BordroEmployeeEditor";
import CreateEditEmployeeModal from "../../employee/component/CreateEditEmployeeModal";
import DocumentConditionalSpec from "../../common/DocumentConditionalSpec";
import ApproveDocumentViewer from "../../approval/helper/ApproveDocumentViewer";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import base64_arraybuffer from "base64-arraybuffer";
import DocumentDeclineSpec from "../../common/DocumentDeclineSpec";

class BordroTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            alert: null,
            selection: [],
            displayTcknDublicates: false,
            pageList: null,
            showOnlyMissing: false,
            showOnlyDublicates: false,
            showOnlySents: false,
            manual: false,
            content: [],
            treeData: [],
            conditionalSelection: [],
            declineSelection: [],
            conditionalSelectedAll: false,
            declineSelectedAll: false,
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            searchKey: "",
            temporaryRow: '',
            showDownloadButton: false,
            detailedDepartment: false,
            selectedAuthorizationWay: authorizationWays.DEPARTMENT
        };

        this.fetchData = this.fetchData.bind(this);
        this.deleteRow = this.deleteRow.bind(this);
        this.isSelected = this.isSelected.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.changeSelection = this.changeSelection.bind(this);
        this.setSelectedDepts = this.setSelectedDepts.bind(this);
        this.toggleCheckboxChange = this.toggleCheckboxChange.bind(this);
        this.toggleConditionalChange = this.toggleConditionalChange.bind(this);
        this.toggleDeclineChange = this.toggleDeclineChange.bind(this);
        this.changeConditionalSelection = this.changeConditionalSelection.bind(this);
        this.changeDeclineSelection = this.changeDeclineSelection.bind(this);
        this.isConditionalAllowed = this.isConditionalAllowed.bind(this);
        this.isDeclineAllowed = this.isDeclineAllowed.bind(this);
        this.setMediaViewer = this.setMediaViewer.bind(this);
        this.previewBordro = this.previewBordro.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
        this.downloadContent = this.downloadContent.bind(this);
        this.onAuthorizationWaysChange = this.onAuthorizationWaysChange.bind(this);
    }

    componentWillMount() {
        this.setState({jobTitlesData: getJobTitles()});
        if (this.props.selection && this.props.selection.length > 0) {
            this.setState({selection: this.props.selection});
        }
        this.readTitleAll();
        let self = this;
        self.readDepartmentTreeData();
    }

    readDepartmentTreeData = () => {
        let self = this;
        self.setState({treeData: []});
        let params = {
            url: '/department/readDepartmentTreeData',
            method: "post",
            data: self.state.selectedAuthorizationWay
        };
        requestK(params)
            .then(function (response) {
                self.setState({treeData: response.data});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    async onAuthorizationWaysChange(event) {
        let self = this;
        await self.setState({selectedAuthorizationWay: event.target.value});
        self.readDepartmentTreeData();
        self.handleSearch();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.manual) {
            this.setState({manual: nextProps.manual});
            this.props.isManual(false);
        }
    }

    async fetchData(accessor, direction, page, size) {
        try {
            if (accessor === "date") {
                accessor = undefined;
                direction = undefined;
            }

            const sortStr = accessor ? `&sort=${accessor},${direction}` : '&sort=id,desc';
            const searchQuery = this.state.searchQuery ? `&${this.state.searchQuery}` : '';
            let url = `/bordro/readAllBordroBySessionId?bordroSessionId=${this.props.bordroSession.id}` +
                `&showOnlyMissing=${this.state.showOnlyMissing}` +
                `&showOnlyDublicates=${this.state.showOnlyDublicates}` +
                `&showOnlySents=${this.state.showOnlySents}` +
                `&page=${page}&size=${size}${searchQuery}${sortStr}`;

            url = this.setSelectedDepts(url);

            const params = {
                url,
                method: "get",
            };

            this.setState({ loading: true, searching: true, manual: this.manual !== true ? this.manual : false });

            const response = await requestK(params);
            const responseData = response.data || {};

            if (responseData.selectedIdList) {
                try {
                    const selectedIdResponse = await requestK({
                        url: '/bordro/getBordroIdsForSelectedIds',
                        method: "post",
                        data: responseData.selectedIdList,
                    });
                    this.setState({ conditionallyOrDeclinableTotalIds: selectedIdResponse ? selectedIdResponse.data : [] }, () => {
                        this.props.totalPdfCountIdList(selectedIdResponse ? selectedIdResponse.data : []);
                    });
                } catch (error) {
                    this.setState({ conditionallyOrDeclinableTotalIds: [] });
                }
            } else {
                this.setState({ conditionallyOrDeclinableTotalIds: [] });
            }

            const content = responseData.page?.content || [];
            this.setState({
                page,
                size,
                total: responseData.page?.totalElements || 0,
                content,
                totalIdList: response.data ? response.data.selectedIdList : [],
                searching: false,
                loading: content.length > 0 || !this.props.isPdfsParsing ? false : true,
            });

            this.updateConditionalList(content);
            this.updateDeclineList(content);
            this.props.tableLoadingCompleted();

        } catch (error) {
            this.setState({ loading: false, searching: false });
            this.props.tableLoadingCompleted();
            showAxiosError(error);
        }
    }

    readTitleAll = () => {
        let self = this;
        let params = {
            url: `/employeecustom/readTitleAll`,
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                self.setState({titleList: response.data});
            })
            .catch(function (error) {
            });
    };

    updateConditionalList = (content) => {
        let conditionalSelection = this.state.conditionalSelection;
        content && content.map(doc => {
            if (doc.conditionalApproveAllowed) {
                conditionalSelection.push(doc.id);
            }
        });
        this.setState({conditionalSelection});
        this.props.changeOnConditionalSelection(conditionalSelection);
    };

    updateDeclineList = (content) => {
        let declineSelection = this.state.declineSelection;
        content && content.map(doc => {
            if (doc.declineAllowed) {
                declineSelection.push(doc.id);
            }
        });
        this.setState({declineSelection});
        this.props.changeOnDeclineSelection(declineSelection);
    };

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true,
            selection: [],
            conditionalSelection: [],
            declineSelection: [],
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    setSelectedDepts(url) {
        let selectedDepts = this.state.selectedDepts;
        if (selectedDepts) {
            Object.keys(selectedDepts).map(dept => {
                if (selectedDepts[dept]) {
                    if (this.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE) {
                        url = url + '&employee_expenditureDepartment_id=' + dept;
                    } else {
                        url = url + '&employee_department_id=' + dept;
                    }
                }
            });
        }
        return url;
    }

    deleteRow(item) {
        let self = this;
        let params = {
            url: `/bordro/deleteBordro`,
            method: "post",
            data: item.id
        };
        self.setState({disabledDeleteBtn: true});
        requestK(params)
            .then(function () {
                self.setState({
                    manual: true,
                    alert: null,
                    disabledDeleteBtn: false,
                    selection: [],
                    conditionalSelection: [],
                    declineSelection: [],
                    conditionalSelectedAll: false,
                    declineSelectedAll: false,
                });
                Alert.success("Bordro silindi.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
                self.props.load();
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDeleteBtn: false});
        });
    }

    cancelSendingBordro(item) {
        let self = this;
        let params = {
            url: `/bordro/cancelSendingBordro`,
            method: "post",
            data: item.id
        };
        self.setState({disabledCancelSendingBtn: true});
        requestK(params)
            .then(function () {
                self.setState({
                    manual: true,
                    alert: null,
                    disabledCancelSendingBtn: false,
                    selection: [],
                    conditionalSelection: [],
                    declineSelection: [],
                    conditionalSelectedAll: false,
                    declineSelectedAll: false,
                });
                Alert.success("Bordro Gnderimi ptal Edildi.", {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
                self.props.load();
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledCancelSendingBtn: false});
        });
    }

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.content;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection});
            this.props.addSelectionToState(selection);
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({selection: selection});
            this.props.addSelectionToState(selection);
        }
    }

    toggleConditionalChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = [];
        self.state.content.map(emp => items.push(emp.id));
        let selection = self.state.conditionalSelection;
        if (row === "all") {
            selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item);
                });
            }
            selection = Array.from(new Set(selection));
            self.setState({conditionalSelection: selection, conditionalSelectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            selection = Array.from(new Set(selection));
            self.setState({
                conditionalSelection: selection,
                conditionalSelectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
        this.props.changeOnConditionalSelection(selection);
    }

    toggleDeclineChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = [];
        self.state.content.map(emp => items.push(emp.id));
        let selection = self.state.declineSelection;
        if (row === "all") {
            selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item);
                });
            }
            selection = Array.from(new Set(selection));
            self.setState({declineSelection: selection, declineSelectedAll: checked});
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            selection = Array.from(new Set(selection));
            self.setState({
                declineSelection: selection,
                declineSelectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false)
            });
        }
        this.props.changeOnDeclineSelection(selection);
    }

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }

    isConditionalAllowed(id) {
        return (
            (this.state.conditionalSelection && this.state.conditionalSelection.indexOf(id) !== -1)
        );
    }

    isDeclineAllowed(id) {
        return (
            (this.state.declineSelection && this.state.declineSelection.indexOf(id) !== -1)
        );
    }

    changeSelection(selection) {
        this.setState({selection: selection});
        this.props.addSelectionToState(selection);
    }

    changeConditionalSelection(conditionalSelection) {
        this.setState({conditionalSelection: conditionalSelection});
        this.props.changeOnConditionalSelection(conditionalSelection);
    }

    changeDeclineSelection(declineSelection) {
        this.setState({declineSelection: declineSelection});
        this.props.changeOnDeclineSelection(declineSelection);
    }

    setMediaViewer() {
        this.setState({url: null, mediaModal: false});
    }

    previewBordro(row) {
        let self = this;
        let params = {
            url: `/bordro/previewBordroContent`,
            responseType: 'arraybuffer',
            method: "post",
            data: row.id
        };
        self.setState({mediaModal: true, temporaryRow: row, showDownloadButton: true});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                let base64Data = "";
                if (blob !== undefined) {
                    let reader = new FileReader();
                    reader.readAsDataURL(blob);
                    reader.onloadend = function () {
                        base64Data = reader.result;
                        self.setState({url: base64Data});
                    }
                }
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({mediaModal: false});
        });
    }

    getTrStyles(row) {
        return (row.employee && row.employee.statusType && row.employee.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    detailedDepartment = () => {
        const b = document.getElementById('departmentContainer');
        this.setState({
            detailedDepartment: !this.state.detailedDepartment
        }, () => {
            if (this.state.detailedDepartment) {
                b.style.height = 'auto';
            } else {
                b.style.height = '200px';
            }
        });

    }

    downloadContent(row, index) {
        let self = this;
        let params = {
            url: `/bordro/previewBordroContent`,
            responseType: 'arraybuffer',
            method: "post",
            data: row.id
        };
        self.setState({disabledDownloadBtn: true, selectedIndex: index});
        requestK(params)
            .then(function (response) {
                let blob = new Blob([response.data], {type: response.headers["content-type"]});
                var link = document.createElement('a');
                link.href = window.URL.createObjectURL(blob);
                let randomNum = Math.floor(Math.random() * 100000);
                let createdAt = !!row.createdAt ? ("_" + row.createdAt.toString().substring(0, row.createdAt.indexOf("T"))) : "";
                let fileName = "Bordro" +
                    (row.parsedTckn ? ("_" + row.parsedTckn + "_") : "_") +
                    row.term.split("-")[0] + "_" +
                    row.term.split("-")[1] + "_" + createdAt + "_" + randomNum +
                    ".pdf";
                link.download = fileName;
                document.body.appendChild(link);
                if (row.timeStamp != null && row.timeStamp != "") {
                    let formTimeStamp = new Blob([base64_arraybuffer.decode(row.timeStamp)], {type: 'application/octet-stream'});
                    let linkTs = document.createElement('a');
                    linkTs.href = window.URL.createObjectURL(formTimeStamp);
                    linkTs.download = fileName + ".zd";
                    document.body.appendChild(linkTs);
                    linkTs.click();
                }
                link.click();
                self.setState({disabledDownloadBtn: false});
                self.setState({loading: false});
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDownloadBtn: false, title: null});
        });
    }

    render() {
        let pageIdList = [], dublicateList = [], tempList = [];
        if (this.state.content && this.state.content instanceof Array) {
            this.state.content.map(e => {
                if ((!e.employee || !e.employee.name || e.employee.statusType === "ACTIVE") ) {
                    pageIdList.push(e.id);
                }
                if (!tempList.includes(e.parsedTckn)) {
                    tempList.push(e.parsedTckn);
                } else if (!dublicateList.includes(e.parsedTckn)) {
                    dublicateList.push(e.parsedTckn)
                }
            });
        }

        let columnList = [
            {
                Header: (
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={this.changeSelection}/>
                ),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.props.bordroSession &&
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           disabled={(row.employee && row.employee.statusType && row.employee.statusType !== 'ACTIVE') || (DocumentState[row.state].value === 602 || DocumentState[row.state].value === 603)}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>)
            },
            {
                Header: "Ay",
                accessor: "term",
                Cell: row => (
                    <div>
                        {!!row.term && moment(row.term, 'YYYY/MM/DD').format('YYYY/MM')}
                    </div>
                )
            },
            {
                Header: "Lokasyon",
                accessor: "location",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.location} maxLength={25}/></div>
                )
            },
            {
                Header: "TCKN",
                accessor: "parsedTckn",
                Cell: row => (
                    <div>
                        {!(row.employee && row.employee.name) &&
                            <span className="text-danger">{row.parsedTckn}</span>}
                        {(row.employee && row.employee.name) && dublicateList.includes(row.parsedTckn) &&
                            <span className="text-warning">{row.parsedTckn}</span>}
                        {(row.employee && row.employee.name) && !dublicateList.includes(row.parsedTckn) &&
                            <span className="text-success">{row.parsedTckn}</span>}
                    </div>
                )
            },
            {
                Header: "Ad",
                accessor: "employee_name",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.name}
                    </div>
                )
            },
            {
                Header: "Soyad",
                accessor: "employee_surname",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.surname}
                    </div>
                )
            },
            {
                Header: "Departman",
                accessor: "employee_department",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.department && row.employee.department.name}
                    </div>
                )
            },
            {
                Header: "Grev",
                accessor: "employee_jobTitle",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.jobTitle}
                    </div>
                )
            },
            {
                Header: "Kep Adresi",
                accessor: "employee_kepAddress",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.kepAddress}
                    </div>
                )
            },
            {
                Header: "alan Durumu",
                accessor: "employee_statusType",
                Cell: row => (
                    <div>
                        {row.employee && row.employee.statusType === "ACTIVE" &&
                            <span className="text-success">{"Aktif"}</span>}
                        {row.employee && row.employee.statusType === "PASSIVE" &&
                            <span className="text-danger">{"Pasif"}</span>}
                        {row.employee && row.employee.statusType === "TERMINATED" &&
                            <span className="text-danger">{"Fesih"}</span>}
                        {row.employee && row.employee.statusType === "CANDIDATE" &&
                            <span className="text-danger">{"Aday"}</span>}
                    </div>
                )
            },
            {
                Header: "Uyar Says",
                accessor: "warningCount",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.warningCount}
                    </div>
                )
            },
            {
                Header: "Sayfa Says",
                accessor: "pageCount",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {row.pageCount}
                    </div>
                )
            },
            {
                Header: "Durumu",
                accessor: "state",
                Cell: row => (
                    <div>
                        {row.error && <span className="text-danger">{row.error}</span>}
                        {!row.error &&
                            <span
                                className={"text-" + DocumentState[row.state].messageType}>{DocumentState[row.state].message}</span>}
                    </div>
                )
            },
            {
                Header: (this.state.content &&
                    <div>
                        <span style={{color: '#007acc'}}>artl Onay</span>
                        <br/>
                        <DropdownCheck selectionIdList={this.state.conditionalSelection}
                                       totalIdList={this.state.conditionallyOrDeclinableTotalIds}
                                       pageIdList={pageIdList}
                                       onChange={this.changeConditionalSelection}
                                       isConditional={true}/>
                    </div>),
                accessor: "conditional",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" style={{width:'60px'}} checked={this.isConditionalAllowed(row.id)}
                           disabled={(row.employee && row.employee.statusType && row.employee.statusType !== 'ACTIVE') || (DocumentState[row.state].value === 602 || DocumentState[row.state].value === 603) || stateCompare(row.state, ">=", "PDF_SIGNED") }
                           onChange={(e) => this.toggleConditionalChange(e, row, "id", index)}/>)
            },
            {
                Header: (this.state.content &&
                    <div>
                        <span style={{color: '#408f57'}}>Reddedilebilir</span>
                        <br/>
                        <DropdownCheck selectionIdList={this.state.declineSelection}
                                       totalIdList={this.state.conditionallyOrDeclinableTotalIds}
                                       pageIdList={pageIdList}
                                       onChange={this.changeDeclineSelection}
                                       isDeclinable={true}/>
                    </div>),
                accessor: "decline",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.content &&
                    <input type="checkbox" style={{width:'75px'}} checked={this.isDeclineAllowed(row.id)}
                           disabled={(row.employee && row.employee.statusType && row.employee.statusType !== 'ACTIVE') || (DocumentState[row.state].value === 602 || DocumentState[row.state].value === 603) || stateCompare(row.state, ">=", "PDF_SIGNED") }
                           onChange={(e) => this.toggleDeclineChange(e, row, "id", index)}/>)
            },
            {
                Header: "lemler",
                accessor: "",
                sortable: false,
                Cell: (row, index) => (
                    <div>
                        {!this.props.isKepOwner &&
                            <div className="btn-group" style={{padding: 0, margin: 0}}>
                                <div className="dropdown" style={{padding: 0, margin: 0}}>
                                    <button className="MenuBtn2 listt btn btn-default"
                                            style={{WebkitTextFillColor: 'white'}}
                                            type="button" data-toggle="dropdown">Senz
                                    </button>
                                    <div className="dropdown-menu" aria-labelledby={"dropdownMenuButton_" + row.id}>
                                        <a className="dropdown-item "
                                           onClick={() => this.previewBordro(row)}>
                                            <i className="fa fa-search"/>Grntle
                                        </a>
                                        {!(this.state.disabledDownloadBtn && index === this.state.selectedIndex) &&
                                            <a className="dropdown-item"
                                               onClick={() => this.downloadContent(row)}>
                                                <i className="fa fa-download"/>Bordro ndir
                                            </a>}
                                        {!(this.state.disabledDownloadBtn && index === this.state.selectedIndex) &&
                                            (DocumentState[row.state].value === 610 || (DocumentState[row.state].value >= 630 && DocumentState[row.state].value <= 660)) &&
                                            <a className="dropdown-item"
                                               onClick={() => downloadApproveDocument(this, row, index, "BORDRO")}>
                                                <i className="fa fa-download"/>Onay Belgesini ndir
                                            </a>}
                                        {stateCompare(row.state, "<", "PDF_SIGNED") &&
                                            <a className="dropdown-item"
                                               onClick={() => this.setState({
                                                   employeeId: row.employee ? row.employee.id : null,
                                                   tckn: row.parsedTckn,
                                                   showModal: true
                                               })}>
                                                <i className="fa fa-at"/>{row.employee && row.employee.id ? "Alc Dzenle" : "Alc Yeni Ekle"}
                                            </a>}
                                        {stateCompare(row.state, ">=", "MIME_SIGNED") && stateCompare(row.state, "<", "APPROVED_BY_EMPLOYEE") &&  !stateCompare(row.state, "=", "OBJECTION") &&
                                            <a className="dropdown-item"
                                               onClick={() => this.setState({
                                                   conditionalDocumentId: row.id,
                                                   conditionalDocumentStatus: this.state.conditionalSelection.includes(row.id),
                                                   showConditionalChangeModal: true
                                               })}>
                                                <i className="fa fa-commenting"/>artl Onay Hakk Deitir
                                            </a>}
                                        {stateCompare(row.state, ">=", "MIME_SIGNED") && stateCompare(row.state, "<", "APPROVED_BY_EMPLOYEE") &&  !stateCompare(row.state, "=", "OBJECTION") &&
                                            <a className="dropdown-item"
                                               onClick={() => this.setState({
                                                   declineDocumentId: row.id,
                                                   declineDocumentStatus: this.state.declineSelection.includes(row.id),
                                                   showDeclineChangeModal: true
                                               })}>
                                                <i className="fa fa-commenting"/>Reddet Hakk Deitir
                                            </a>}
                                        {row.employee && row.employee.name &&
                                            <a className="dropdown-item"
                                               onClick={() => this.setState({stateDetail: row})}>
                                                <i className="fa fa-info-circle"/>Durum Detay
                                            </a>}
                                        {stateCompare(row.state, "<", "PDF_SIGNED") &&
                                            <a className="dropdown-item"
                                               onClick={() =>
                                                   this.setState({
                                                       alert: (
                                                           <SweetAlert showCancel warning
                                                                       disabled={this.state.disabledDeleteBtn}
                                                                       confirmBtnText="Evet"
                                                                       cancelBtnText="Hayr"
                                                                       confirmBtnBsStyle="danger"
                                                                       cancelBtnBsStyle="default"
                                                                       title="Emin misiniz?"
                                                                       onConfirm={() => {
                                                                           this.deleteRow(row)
                                                                       }}
                                                                       onCancel={() => this.setState({alert: null})}>
                                                               cret Bordrosu silinecek.
                                                           </SweetAlert>)
                                                   })
                                               }>
                                                <i className="fa fa-close"/>Sil
                                            </a>
                                        }

                                        {(stateCompare(row.state, "=", "INSERTED_TO_FLOW") || stateCompare(row.state, "=", "FLOW_TIMEOUT")) &&
                                            <a className="dropdown-item"
                                               onClick={() =>
                                                   this.setState({
                                                       alert: (
                                                           <SweetAlert showCancel warning
                                                                       disabled={this.state.disabledCancelSendingBtn}
                                                                       confirmBtnText="Evet"
                                                                       cancelBtnText="Hayr"
                                                                       confirmBtnBsStyle="danger"
                                                                       cancelBtnBsStyle="default"
                                                                       title="Emin misiniz?"
                                                                       onConfirm={() => {
                                                                           this.cancelSendingBordro(row)
                                                                       }}
                                                                       onCancel={() => this.setState({alert: null})}>
                                                               Bordro Gnderimi ptal Edilecek.
                                                           </SweetAlert>)
                                                   })
                                               }>
                                                <i className="fa-solid fa-rectangle-xmark"></i>Gnderimi ptal Et
                                            </a>
                                        }

                                    </div>
                                </div>
                            </div>}
                        {this.props.isKepOwner &&
                            <div style={{textAlign: 'left', webkitTextFillColor: 'white'}}>
                                <button title="cret Bordrosu Grntle" className="btn-icon btn btn-info btn-sm"
                                        style={{margin: '0px 3px'}}
                                        onClick={(e) => this.previewBordro(row)}>
                                    <i className="now-ui-icons ui-1_zoom-bold"/>
                                </button>
                                <button title="Alc Bilgisi Dzenle" className="btn-icon btn btn-success btn-sm"
                                        style={{margin: '0px 3px'}}
                                        onClick={e => this.setState({selected: row})}>
                                    <i className="now-ui-icons ui-2_settings-90"/>
                                </button>
                                {stateCompare(row.state, "<", "MIME_SIGNED") &&
                                    <button title="cret Bordrosu Sil" className="btn-icon btn btn-danger btn-sm"
                                            style={{margin: '0px 3px'}} onClick={e => {
                                        this.setState({
                                            alert: (
                                                <SweetAlert showCancel warning
                                                            confirmBtnText="Evet"
                                                            cancelBtnText="Hayr"
                                                            confirmBtnBsStyle="danger"
                                                            cancelBtnBsStyle="default"
                                                            title="Emin misiniz?"
                                                            disabled={this.state.disabledDeleteBtn}
                                                            onConfirm={() => this.deleteRow(row)}
                                                            onCancel={() => this.setState({alert: null})}>
                                                    cret Bordrosu silinecek.
                                                </SweetAlert>
                                            )
                                        });
                                    }}>
                                        <i className="now-ui-icons ui-1_simple-remove"/>
                                    </button>}
                            </div>}
                    </div>
                )
            }
        ];
        let columns = renewListForPrivileges(this, columnList, "accessor", this.props.isKepOwner);

        const fieldList = [
            {
                label: "Ad",
                accessor: "employee_name",
                type: "text",
                operator: "=",
            },
            {
                label: "Soyad",
                accessor: "employee_surname",
                type: "text",
                operator: "=",
            },
            {
                label: "TCKN",
                accessor: "parsedTckn",
                type: "numericText",
                operator: "=",
            },
            {
                label: "Durum",
                accessor: "state",
                type: "select",
                operator: "=",
                selectData: stateData(this.props.isKepOwner)
            },
            {
                label: "Departman",
                accessor: "employee_department_name",
                type: "text",
                operator: "=",
            },
            {
                label: "Grevi",
                accessor: "employee_jobTitle",
                type: "select",
                operator: "=",
                selectData: this.state.jobTitlesData
            },
            {
                label: "Cinsiyet",
                accessor: "employee_gender",
                type: "select",
                operator: "=",
                selectData: genderData()
            },
            {
                label: "alma ekli",
                accessor: "employee_employmentStatus",
                type: "text",
                operator: "=",
            },
            {
                label: "KEP Adresi",
                accessor: "employee_kepAddress",
                type: "text",
                operator: "=",
            },
            {
                label: "e-Posta Adresi",
                accessor: "employee_email",
                type: "text",
                operator: "=",
            },
            {
                label: "Telefon Numaras",
                accessor: "employee_telephone",
                type: "text",
                operator: "=",
            },
            {
                label: "alan Durum",
                accessor: "employee_statusType",
                type: "select",
                operator: "=",
                selectData: statusTypeFilterData(),
            },
            {
                label: "Lokasyon Bilgisi",
                accessor: "location",
                type: "text",
                operator: "=",
            },
            {
                label: "Uyar Says",
                accessor: "warningCount",
                type: "numericText",
                operator: "=",
            },
        ];
        let fields = renewListForPrivileges(this, fieldList, "accessor", this.props.isKepOwner);

        return (
            <div className="row" style={{paddingTop: "15px"}}>
                {this.state.treeData && this.state.treeData.length > 0 &&
                    <div className="col-xs-12 col-md-6" id='departmentContainer' style={{padding: 0}}
                         onClick={() => this.detailedDepartment()}>
                        <Col sm={12}>
                            <div style={{textAlign: 'center'}} onChange={this.onAuthorizationWaysChange}>
                                <input sm={6} type="radio" value={authorizationWays.DEPARTMENT}
                                       name="authorizationWay"
                                       checked={this.state.selectedAuthorizationWay === authorizationWays.DEPARTMENT}/> Departman
                                <input sm={6} type="radio" value={authorizationWays.EXPENDITURE}
                                       name="authorizationWay"
                                       checked={this.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE}
                                       style={{marginLeft: 20}}
                                /> Masraf Yeri
                            </div>
                        </Col>
                        <Col id='departmentHeader' style={{padding: 0}}>
                            <label id='departmentHeaderContent'>Daha Fazla Gster
                                <span style={{marginLeft: '5px', fontSize: '14px'}}><i className="fas fa-chevron-down"/></span>
                            </label>
                        </Col>
                        <DepartmentSelectionTree treeData={this.state.treeData}
                                                 selectedDepts={this.state.selectedDepts}
                                                 selectedAuthorizationWay={this.state.selectedAuthorizationWay}
                                                 loading={e => this.setState({loading: e})}
                                                 onSelect={e => this.setState({selectedDepts: e, manual: true})}
                                                 changeOnConditionalList={e => this.setState({conditionalSelection: e})}
                                                 changeOnDeclineList={e => this.setState({declineSelection: e})}/>
                    </div>}
                <div
                    className={(this.state.treeData && this.state.treeData.length > 0) ? "col-xs-12 col-md-6" : "col-xs-12 col-md-12"}>
                    <DetailedSearch fields={fields}
                                    loading={this.state.searching}
                                    onSearch={this.handleSearch}/>
                </div>
                <div className="col-xs-12 col-md-12">
                    {/*{(this.props.bordroSessionInfo && this.props.bordroSessionInfo.unpairedCount > 0 ||*/}
                    {/*    this.props.bordroSessionInfo && this.props.bordroSessionInfo.dublicatedCount > 0) &&*/}
                    <div className="row">
                        {this.props.bordroSessionInfo && this.props.bordroSessionInfo.unpairedCount > 0 &&
                            <Col sm={12}>
                                <Input type="checkbox" checked={this.state.showOnlyMissing}
                                       onChange={(e) => {
                                           if (e.target.checked) {
                                               this.setState({
                                                   showOnlyMissing: e.target.checked,
                                                   showOnlyDublicates: false,
                                                   selection: [],
                                                   conditionalSelection: [],
                                                   declineSelection: [],
                                                   manual: true
                                               });
                                           } else {
                                               this.setState({
                                                   showOnlyMissing: e.target.checked,
                                                   selection: [],
                                                   conditionalSelection: [],
                                                   declineSelection: [],
                                                   manual: true
                                               });
                                           }
                                       }}/>
                                <Label>Sadece sahibi bulunamayan cret bordrolarn gster</Label>
                            </Col>
                        }
                        {this.props.bordroSessionInfo && this.props.bordroSessionInfo.dublicatedCount > 0 &&
                            <Col sm={12}>
                                <Input type="checkbox" checked={this.state.showOnlyDublicates}
                                       onChange={(e) => {
                                           if (e.target.checked) {
                                               this.setState({
                                                   showOnlyDublicates: e.target.checked,
                                                   showOnlyMissing: false,
                                                   selection: [],
                                                   conditionalSelection: [],
                                                   declineSelection: [],
                                                   manual: true
                                               });
                                           } else {
                                               this.setState({
                                                   showOnlyDublicates: e.target.checked,
                                                   selection: [],
                                                   conditionalSelection: [],
                                                   declineSelection: [],
                                                   manual: true
                                               });
                                           }
                                       }}/>
                                <Label>Sadece mkerrer tckn olanlar gster</Label>
                            </Col>
                        }
                        <Col sm={12}>
                            <Input type="checkbox" checked={this.state.showOnlySents}
                                   onChange={(e) => {
                                       this.setState({
                                           showOnlySents: e.target.checked,
                                           manual: true
                                       });
                                   }}/>
                            <Label>Sadece tm gnderilmi bordrolar gster</Label>
                        </Col>
                        {this.state.loading &&
                            <Col sm={12} style={{display:"flex"}}>
                                <div style={{marginRight: "10px"}}>Kaytlar getiriliyor</div>
                                <div>
                                    <i className="fa fa-spinner fa-spin"/>
                                </div>
                            </Col>
                        }
                    </div>
                    {/*}*/}
                    <div className="row">
                        <Table columns={columns}
                               content={this.state.content}
                               loading={this.state.loading || this.props.loading}
                               total={this.state.total}
                               page={this.state.page}
                               manual={this.state.manual}
                               size={this.state.size}
                               options={["5", "10", "20", "50", "100"]}
                               getTrStyles={this.getTrStyles}
                               fetchData={this.fetchData}
                        />
                    </div>
                </div>
                {this.state.stateDetail &&
                    <StateDetail detail={this.state.stateDetail} documentType={"Bordro"}
                                 toggle={() => this.setState({stateDetail: undefined})}/>}
                <BordroEmployeeEditor bordro={this.state.selected}
                                      toggle={() => this.setState({selected: undefined})}
                                      onBordroUpdated={() => this.setState({manual: true})}/>
                {this.state.mediaModal &&
                    <ApproveDocumentViewer modal={this.state.mediaModal}
                                           url={this.state.url}
                                           title="Bordro"
                        // listener={this.setMediaViewer}
                                           closeModal={this.setMediaViewer}
                                           downloadContent={this.downloadContent}
                                           temporaryRow={this.state.temporaryRow}
                                           showDownloadButton={this.state.showDownloadButton}
                    />}
                {this.state.showConditionalChangeModal &&
                    <DocumentConditionalSpec modal={this.state.showConditionalChangeModal}
                                             status={this.state.conditionalDocumentStatus}
                                             list={this.state.conditionalSelection}
                                             sessionId={this.props.bordroSession.id}
                                             id={this.state.conditionalDocumentId}
                                             toggleModal={(flag, manual) =>
                                                 this.setState({showConditionalChangeModal: flag}, () => {
                                                     if (manual) {
                                                         this.props.load()
                                                     }
                                                 })}/>}
                {this.state.showDeclineChangeModal &&
                    <DocumentDeclineSpec modal={this.state.showDeclineChangeModal}
                                             status={this.state.declineDocumentStatus}
                                             list={this.state.declineSelection}
                                             sessionId={this.props.bordroSession.id}
                                             id={this.state.declineDocumentId}
                                             toggleModal={(flag, manual) =>
                                                 this.setState({showDeclineChangeModal: flag}, () => {
                                                     if (manual) {
                                                         this.props.load()
                                                     }
                                                 })}/>}
                {this.state.showModal &&
                    <CreateEditEmployeeModal
                        id={this.state.employeeId} tckn={this.state.tckn} modal={this.state.showModal}
                        titleList={this.state.titleList}
                        hasGrant={this.props.hasGrant}
                        sidebarMenus={this.props.sidebarMenus} isKepOwner={this.props.isKepOwner}
                        toggleModal={(flag, manual) => this.setState({showModal: flag}, () => {
                            if (manual) {
                                this.props.load()
                            }
                        })}/>}
                {this.state.alert}
            </div>
        )
    }
}

export default BordroTable;